.ticket-show-page {
	.sticky-header {
		height: 38px;
		width: calc(100% + 32px);
		position: relative;
		left: -16px;
		&.active {
			box-shadow: 0px 4px 4px $list-item-shadow;
			background: $white;
			z-index: 10;
		}
		> #sticky-title {
			margin-left: 48px;
		}
		#export-activities {
			margin-right: 16px;
			margin-top: -8px;
		}
	}

	.timeline-container {
		height: calc(100% + 32px) !important;
		margin-top: -38px;
		
		&.empty { overflow: inherit; }

		.timeline-list .activity-entry {
			padding-top: 16px !important;
			.activity-content > b {
				text-transform: capitalize;
			}
		}

		.original-actor {
			&::before {
				content: "(";
			}
			&::after {
				content: ")";
			}
		}
	}
}
