
.bulk-cr-hover {
	border: 1px solid $inputFocusBorder !important;
}

.canned-responses-header {
	@include flexbox-simple(space-between, center);
	.btn-link.new {
		@include flexbox-simple(flex-start, center);
		@include border-style($border-dark-color);
		padding: 5px 16px;
		&:hover {
			background: $background-color-2 !important;
		}
		span {
			margin-left: 5px;
			color: $primary-text-color-1;
			font-family: $baseFontFamily;
		}
	}
}

.modal-canned-response-carousel-container {
	.canned-response-search {
		margin-top: 16px;
		margin-bottom: 32px;
		svg.search-icon,
		svg.close-icon {
			fill: $disabled-text-color;
		}
		.search-item-input::-ms-clear {
			display: none !important;
			width: 0 !important;
			height: 0 !important;
		}
	}
	.canned-response-folder {
		width: 40%;
		margin-bottom: 24px;
		position: relative;
		margin-top: 32px;
		.cr-autocomplete,
		.cr-dropdown {
			width: 168px;
			.ember-power-select-trigger {
				display: flex;
				align-items: center;
			}
			.ember-power-select-search {
				border-bottom: 1px solid $app-border-primary;
				padding: 4px;
				input {
					padding-left: 40px;
					border: 0;
					&::-ms-clear {
						display: none !important;
						width: 0 !important;
						height: 0 !important;
					}
				}
				.search-container {
					top: 11px;
					svg {
						fill: $disabled-text-color;
					}
				}
			}
			.ember-power-select-status-icon {
				fill: $app-primary-black;
			}
			.ember-power-select-option {
				color: $app-icon-color;
				text-align: left;
				padding-right: 25px;
				&[aria-selected=true] {
					color: $app-primary;
				}
			}
		}
		.folder-icon {
			position: absolute;
			top: 8px;
			margin-left: 15px;
			bottom: 2px;
			z-index: 1;
			fill: $app-icon-color;
		}
		label {
			display: none;
		}
		.ember-power-select-selected-item {
			padding-left: 24px;
			width: 130px;
			display: inline-block;
			@extend .ellipsis;
		}
	}
	.recent-response {
		font-weight: $text-weight-medium;
		margin-bottom: 10px;
		span {
			font-size: $baseFontSize;
			color: $app-primary-black;
		}
		.ui-accordion-list {
			margin-top: 24px;
		}
	}
	.empty-state {
		margin-top: 200px;
		#no-tickets {
			color: $app-form-label-color;
			font-size: $title-normal;
			margin-top: 16px;
		}
		.new {
			@include flexbox-simple(flex-start, center);
			margin-top: 16px;
			span {
				margin-left: 5px;
			}
		}
		img.ember-svg:focus {
			@extend .bulk-cr-hover;
		}
	}
	.ui-accordion-list {
		.ui-accordion-item {
			margin-bottom: 8px;
			background: $white;
			@include border-style($border-dark-color);
			line-height: $baseLineHeight;
			&:focus,
			&:focus-within {
				@extend .bulk-cr-hover;
			}
			&:hover {
				@extend .bulk-cr-hover;
				.insert-canned-response {
					transition: .2s linear;
					opacity: 1 !important;
				}
			}
			.pull-left {
				background: $border-light-color;
				border-radius: 4px;
				margin-right: 8px;
				svg.chevron-icon {
					@include flexbox;
					margin: 4px;
				}
				&.is-expanded {
					svg {
						fill: $white;
					}
					background: $inputFocusBorder;
				}
			}
			.item-title {
				display: block;
				margin-left: 8px;
				font-size: $title-small;
				color: $primary-text-color-1;
				font-weight: $text-weight-semi-bold;
				padding: 2px;
				&::after { // safari fix
					content: '';
					display: block;
				}
			}
			.accordion-header {
				@include flexbox-simple(space-between, center);
				padding: 15px;
				.ui-accordion-header {
					button {
						width: 400px;
					}
					.ember-tooltip {
						word-break: break-word;
					}
				}
				.alert-danger {
					background: none;
					svg {
						vertical-align: bottom;
						margin: 4px;
					}
					&:focus {
						@include border-style($inputFocusBorder);
						margin-top: -1px;
					}
					&:after {
						margin-left: 200px;
					}
				}
				.insert-canned-response.btn-link {
					@include flexbox-simple(flex-start, center);
					@include border-style($border-dark-color);
					font-family: $baseFontFamily;
					padding: 5px 16px 5px 16px;
					opacity: 0;
					span {
						display: inline-block;
						max-width: 32px;
						@extend .ellipsis;
						color: $primary-text-color-1;
					}
					&:hover {
						background: $background-color-2 !important;
					}
					&:focus,
					&:focus-within {
						transition: .2s linear;
						opacity: 1;
						@extend .bulk-cr-hover;
					}
					svg {
						margin-right: 8px;
					}
				}
			}
			.accordion-panel .ui-accordion-panel-wrapper {
				transition: height .2s ease-in, visibility .2s ease-in; // overriding the default set transition to avoid some glitch when expanding
				padding-left: 0 !important;
			}
			.accordion-panel .ui-accordion-panel-content {
				padding: 8px 15px 8px 24px;
				background: $background-color-2;
				border-bottom-right-radius: 4px;
				border-bottom-left-radius: 4px;
				.description-text .text {
					font-size: $text-normal;
				}
				.attachment-thumb {
					margin-top: 24px;
					.attachment-header {
						margin-bottom: 5px;
						span {
							font-size: $text-normal;
						}
					}
					.attachment-content {
						@include flexbox;
						flex-wrap: wrap;
					}
					.attachment-item {
						width: 320px;
						padding: 10px 16px;
						background: $app-white;
						@include border-style($app-border-primary);
						align-items: center;
						margin-bottom: 8px;
						&:hover {
							@extend .bulk-cr-hover;
						}
						.file-info {
							@include flexbox;
							@include justify-content(space-between);
							.file {
								@include flexbox;
								.file-details {
									margin-left: 8px;
									@include flexbox-simple(space-between, stretch);
									width: 85%;
									margin-top: -5px;
									@include flexbox;
									flex-wrap: wrap;
									.name {
										font-size: $text-normal;
										width: 232px;
										vertical-align: top;
										color: $primary-text-color-1;
										@include line-clamp($lines: 2);
										max-height: 3em;
									}
									&:hover .download {
										opacity: 1;
									}
								}
								.size {
									font-size: $text-normal;
									color: $secondary-text-color-1;
								}
							}
						}
						.download {
							width: 10%;
							@include flexbox-simple(flex-start, center);
							opacity: 1;
							.disable-shortcuts-trigger:hover {
								background: $background-color-2;
								border-radius: 4px;
							}
							svg {
								@include flexbox;
								margin: 8px;
								fill: $inputHoverBorder;
							}
						}
					}
				}
			}
		}
	}
}
