.ticket-conversation-load-more--wrapper {
	position: relative;
	margin-top: 16px;
}

.ticket-summary-modal-conversations-shimmer{
	.shimmer-loader{
		padding-bottom: 12px;
	}
}

.ticket-conversation-load-more {
	@include flexbox-simple(center, center);
	border-radius: 20px;
	font-size: $text-normal;
	font-weight: $text-weight-semi-bold;
	height: 24px;
	padding: 0 10px 0 4px;
}

.ticket-conversation-load-more--icon {
	margin: 0 8px 1px 0;
}

.conv-quoted-text-btn{
	background: $app-white;
	height: 16px;
	width: 32px;
	border: 1px solid $app-border-primary;
	border-radius: 30px;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-top: 20px;
}

.ticket-conversation-load-more--parallel-lines {
	position: absolute;
	top: 11px;
	height: 4px;
	width: 100%;
	border-top: 1px solid $border-dark-color;
	border-bottom: 1px solid $border-dark-color;
	z-index: -1;
}
.convo-header label{
	font-weight: $text-weight-semi-bold;
	font-size: $title-normal;
	padding: 0;
	margin: 32px 0 0 0;
}
.ticket-conversation-cards--wrapper {
	flex-direction: column;
}

.ticket-conversation-card {
	background: $section-background-blue;
	padding: 16px;
	border: 1px solid $border-light-blue;
	border-radius: 8px;
	color: $app-grey;
	margin-bottom: 12px;

	&.ticket-conversation-card--private-note {
		background: $app-casablanca-light;
		border: 1px solid $alertPartialBorder;
	}

	&.ticket-conversation-card--public-note {
		background: $app-white;
		border: 1px solid $border-light-color;
	}

	&.ticket-conversation-card--forward {
		background: $background-color-2;
		border: 1px solid $border-light-color;
	}

	&.ticket-conversation-card--agent-response {
		background: $app-white;
		border: 1px solid $border-light-color;
	}

	&.ticket-conversation-card--requester-response {
		background: $section-background-blue;
		border: 1px solid $border-light-blue;
	}

	img {
		max-width: 100%;
	}

	.ticket-note-editor,
	.ticket-forward-editor,
	.ticket-reply-editor {
		border: 2px solid #2C5CC5;
		box-sizing: border-box;
		border-radius: 8px;
		margin-top: 30px;
		padding: 2px;
		.ticket-editor-field.editor-header,
		.ticket-editor-field.editor-footer {
			background: $background-color-2 !important;
		}
		.ticket-responder-avatar {
			margin-left: -6px;
		}
	}
}

.ticket-conversation-card--avatar {
	margin-right: 12px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.ticket-conversation-card--private-icon {
	width: 14px;
	height: 16px;
	margin-top: 2px;
	position: absolute;
	left: -34px;
}

.ticket-conversation-card--desc{
	width: 90%;
}

.ticket-conversation-card--desc-name {
	font-weight: $text-weight-medium;
}

.ticket-conversation-card--desc-reported-time {
	color: $secondary-text-color-1;
	@include line-clamp($lines: 2);
}

.conversation-email-ids-tooltip{
	word-break: break-word;
	max-width: 350px;
	background: $app-light-bg !important;
	color: $primary-text-color-1 !important;
	padding: 12px 16px;
	box-shadow: 0px 2px 4px rgba(18, 52, 77, 0.16), 0px 2px 16px rgba(18, 52, 77, 0.06);
	.conversation-email-tooltip{
		.email-recipient-type{
			white-space: nowrap;
			padding-right: 6px;
			text-align: right;
		}
		tr:not(:last-child){
			.email-list{
				padding-bottom: 8px;
			}
		}
	}
}

.ticket-conversation-card--desc-text {
	margin-top: 16px;
	line-height: 21px;
	position: relative;
	&.feedback-conversation{
		span{
			margin-left: 6px;
			&::after{
				content: "";
				height: 14px;
				margin: 0 13px 0 14px;
				border: 1px solid $border-dark-color;
				width: 0;
			}
		}
		a{
			font-weight: $text-weight-semi-bold;
		}
		div{
			font-size: $title-large;
		}
		.conversation-card-feedback {
			height: 25px;
			.requester-feedback-label {
				display: none;
			}
			.feedback-score {
				&::after {
					border-left: 1px solid $border-light-blue;
				}
			}
			.requester-feedback-link .view-feedback-link {
				font-weight: $text-weight-semi-bold;
				font-size: $title-small;
			}
		}
	}
	.call-duration{
		margin-top: 16px;
	}
	&.conversation-note .phone-call-wrapper{
		margin-bottom: 16px;
		.audio-wrapper{
			margin-bottom: 0;
		}
	}
	.freshdesk_quote{
		border-left: 0;
		font-size: 14px;
		line-height: 22px;
		margin: 7px;
		color: $secondary-text-color-1;
	}
	.private-note-tooltip {
		margin-left: -24px !important;
	}
}

.ticket-conversation-card--attachment {
	padding: 0;
	margin: 16px 0 0 0;
	background: none;
	.attachment-list .attach-files{
		padding: 0;
		margin: 0;
		background: none;
		.attachment-title {
			display: none;
		}
	}
	.list-file-item.cloud-file .file.cloud {
		.file-info {
			display: block;
			margin: 0;
		}
		.file-name {
			max-width: 100%;
		}
	}
}

.ticket-conversation-card--attachment-title {
	color: $app-grey;
	font-size: $text-normal;
	font-weight: $text-weight-medium;
	display: flex;
	align-items: center;
	margin-bottom: 4px;
	span{
		display: flex;
	}
	.ember-tooltip{
		max-width: 210px;
	}
}

.ticket-conversation-card--attachment-icon {
	fill: $app-primary;
	width: 12px;
	height: auto;
	margin-left: 5px;
}

.convo-buttons-container {
	background: $background-color-2;
	border-radius: $radius;
	padding: 12px 18px;
	.ticket-responder-avatar {
		margin-right: 16px;
	}
	button{
		display: flex;
		align-items: center;
		padding: 12px 12px 12px 8px;
		svg{
			margin-right: 8px;
			&.add-note path{
				fill: $app-primary;
			}
		}
	}
}

.ticket-summary-modal{
	&.ember-modal-dialog{
		.ember-modal-header{
			padding-bottom: 18px;
		}
		.no-ticket-conversations{
			display: none !important;
		}
		#ticket-summary-modal-footer{
			width: 100%;
			.convo-buttons-container{
				padding-left: 8px;
			}
		}
	}
}

.ticket-show-page {
	.ticket-conversation-card--attachment {
		.attachment-list-container .attachment-list {
			display: flex;
			flex-wrap: wrap;
			flex-direction: row;
			.list-file-item {
				height: 72px;
				max-height: 72px;
				padding: 16px;
				display: flex;
				width: 45%;
				margin: 8px 8px 0 0;

				div svg.file-icon{
					width: 24px;
					height: 24px;

				}
				.delete-file{
					margin: 0;
					padding: 0 0 0 8px;
				}
				.file-info{
					margin: 0 0 0 10px;
					width: 80%;
					align-self: flex-start;
					.file {
						max-width: 100%;
						top: 0;
						cursor: pointer;
						.file-name{
							font-weight: $text-weight-medium;
						}
						&:not(.local) {
							top: -2px;
						}
					}
					.file-size {
						display: block;
						top: -10px;
					}
					.file-provider-type {
						display: block;
						margin: 0 !important;
					}
				}
				&.cloud-file .file .file-name {
					max-width: 95%;
				}
			}
			.max-size-file-list{
				margin: 0;
			}
		}
	}
}
