.new-tickets-page {

	$templateFieldMaxWidth: 848px;
	$formContainerRightPadding: 36px;
	$formContainerLeftPadding: 32px;
	// Page specific overrides
	.page-content {
		width: calc(100% - 334px - 16px - 16px - 48px);
		margin: 0 0 $page-margin-bottom;
		min-height: 100vh; // will remove this
		background: $white;
		overflow-y: scroll;
	}

	// Page specific styles
	.page-sidebar {
		right: 0;
		height: 100%;
		position: fixed;
		width: 414px;
	}

	.quick-menu-navigator {
		background: $white;
		width: 50px;
		padding: 22px 9px;
	}

	.form-footer {
		background: $white;
		position: fixed;
		left: 0;
		right: 414px;
		bottom: 0;
		text-align: right;
		padding: 22px 40px;
		z-index: 9;
		box-shadow: 0px -4px 15px rgba(0, 0, 0, 0.06);
		.form-submit-btn {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
		}

		.btn-primary {
			&.disabled,
			&[disabled] {
				background: linear-gradient(180deg, $btnDisabledColor 4.39%, $btnDisabledLightColor 94.46%) !important;
				opacity: 0.5;
				color: $white !important;
				border: 1px solid $btnDisabledLightColor;
				border-right: 1px solid transparent;
			}
		}
	}

	.new-ticket-form-container {
		padding: 20px $formContainerRightPadding 20px $formContainerLeftPadding;
		margin-bottom: 80px !important;

		.form-title {
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
			margin: 0 0 20px;
		}
	}

	.form-submit-actions {
		top: 0;
		right: 20px;
		min-width: 200px;
		position: absolute;

		.ember-basic-dropdown-trigger {
			position: absolute;
			right: 0;
			top: 22px;
			width: 30px;
			height: 32px;
			background: $btnPrimaryBackground;
			border: 1px solid $btnPrimaryBorder;
			border-top-right-radius: 4px;
			border-bottom-right-radius: 4px;
			&:hover,
			&.ember-basic-dropdown-trigger--above {
				background: $btnPrimaryBackgroundHighlight;
				cursor: pointer;
			}
			&:focus {
				border: $inputFocusBorder;
			}
			svg {
				position: absolute;
				top: 6px;
				left: 4px;
				fill: $white;
			}
			&[aria-disabled=true] {
				border-color: $btnBorder;
				color: $white !important;
				background: linear-gradient(180deg, $btnDisabledColor 4.39%, $btnDisabledLightColor 94.46%) !important;
				border: 1px solid $btnDisabledLightColor;
				border-right: 1px solid transparent;
				opacity: 0.5;
				box-shadow: none;
			}
		}
		.ember-basic-dropdown-content {
			left: auto;
			right: 0;
			margin-top: 18px;
			li {
				button {
					background: none;
					border: none;
					border-radius: 4px;
					padding: 6px 20px 6px 14px;
					font-size: $baseFontSize;
					color: $primary-text-color-1;
					text-align: left;
					width: 100%;
					span {
						font-weight: $text-weight-semi-bold;
					}
					&:hover {
						background: $dropdown-hover;
						cursor: pointer;
					}
				}
			}
		}
	}

	@media (min-width: 1921px) {
		.new-ticket-form-container {
			width: $templateFieldMaxWidth + $formContainerRightPadding + $formContainerLeftPadding;
			margin: 0 auto;
		}

		.form-footer {
			@include flexbox;
			@include align-items(center);
			@include flex-direction(column);
			.form-footer-btn {
				position: relative;
				width: $templateFieldMaxWidth + $formContainerRightPadding + $formContainerLeftPadding;
	
				.ember-basic-dropdown-trigger {
					top: 0px;
					right: -40px;
				}
	
				.ember-basic-dropdown-content {
					top: -102px !important;
					right: -40px;
				}
			}
		}
	}
}

.new-ticket-form [formserv-field-name='plannedEffort'] input,
.new-change-form [formserv-field-name='plannedEffort'] input,
.new-problem-form [formserv-field-name='plannedEffort'] input {
	width: 317px !important;
}

.new-ticket-form-container,
.new-problem-form-container,
.new-change-form-container {
	.association-form-footer {
		background: $background-color-2;
		width: 600px;
		padding: 12px 22px;
		text-align: right;
		position: fixed;
		bottom: 0;
		margin-left: -32px;
		z-index: 99;
	}

	.fr-command.fr-btn svg {
		margin: 8px 5px;
	}

	.new-ticket-form,
	.new-problem-form,
	.new-change-form {
		.attach-files {
			margin: 0px;
			padding-left: 0px;
			background-color: transparent;
			.attachment-list-container-drag-drop {
				max-width: 752px;
			}
			.attachment-list {
				.list-file-item {
					max-width: 530px;
					width: auto;
				}
			}
		}
	}

	.new-problem-form,
	.new-change-form {
		padding-bottom: 32px;

		.attach-files {
			padding-right: 0;

			.attachment-list-container-drag-drop {
				width: auto;
			}
		}

		.associate-asset-card-wrapper .associate-asset-card .asset-card {
			width: 100%;
		}
	}

	.fieldset-wrapper {
		.form-field {
			scroll-margin-top: $global-nav-width + 16px; // 16px(form-field padding) added as buffer
			&.description-field {
				// transparent Froala toolbar
				.froala-editor-container {
					.fr-toolbar  {
						background: transparent;
						border-bottom: none;
					}
					.fr-second-toolbar {
						display: none;
					}
				}
			}

			.date-control {
				width: 200px;
			}

			&.checkbox-field {
				label, input {
					margin-left: 0;
				}
			}

			// Will update with general class specific styles here - @jayasenthil
			&.dependent-select-dropdown-field {
				&+div {
					margin-left: 8px;
					.form-field {
						padding-left: 14px;
						width: calc(535px - 8px);

						&.textarea-field .para-count {
							// adjusting padding added for section fields
							width: calc(100% - 14px);
						}

						&+div {
							.form-field {
								width: calc(535px - 16px);
							}
						}
					}
				}
			}

			&.section-parent-field {
				&+div {
					border-left: 1px solid $primary-border;
					.form-field {
						border-left: 0;
					}
				}
			}
		}
	}
}
