.requester-field-container {
	position: relative;

	#add-cc-btn-container {
		position: absolute;
		top: 24px;
		left: 616px;

		.btn-link {
			padding: 4px 12px;
			box-sizing: border-box;
			font-weight: $text-weight-semi-bold;
			font-size: $text-normal;
		}
	}
}

.ember-modal-dialog .render-component .requester-field-container {
	position: initial;
	display: flex;
	align-items: center;
	justify-content: space-between;

	.form-field {
		flex: 1;
		max-width: 440px;
	}

	#add-cc-btn-container {
		position: initial;
		align-self: end;
		padding-left: 12px;
		min-width: 92px;
	}
}

.ember-modal-dialog .new-ticket-form .requester-field-container {
	#add-cc-btn-container {
		align-self: auto;
		margin-top: 6px;
	}
}