.ticket-show-page {
	.svg-16 {
		height: 16px;
		width: 16px;
	}
	.conversation-actions-container {
		margin-top: 16px;
		.app-icon--small {
			width: 13px;
			height: 13px;
		}

		.convo-header label{
			font-weight: 600;
			font-size: 16px;
			line-height: 21px;
			color: $primary-text-color-1;
		}

		.tkt-convo-buttons-container {
			background: $background-color-2;
			border-radius: 8px;
			padding: 10px;

			button {
				svg {
					width: 14px;
					height: 14px;
					margin-right: 5px;
					margin-top: 2px;
					position: relative;
					top: 1px;
					fill: $linkColor;

					&.ticket-add-note {
						top: 2px;
					}
					&.ticket-details-collab-logo {
						margin-top: 0px;
						width: 18px;
						height: 18px;
					}
				}
			}
			.discuss-btn {
				display: inline-flex;
			}
			.ticket-responder-avatar {
				margin-right: 8px;
			}
		}

		.ticket-responder-avatar {
			display: inline-block;
			vertical-align: middle;
			margin-left: 5px;
		}
	}

	.conversation-editor-container,
	.ticket-conversation-card {
		margin-top: 16px;
		margin-bottom: 0;

		.ticket-forward-editor,
		.ticket-note-editor,
		.ticket-reply-editor {
			background: $background-color-2;
			border: 2px solid $linkColor;
			box-sizing: border-box;
			border-radius: 8px;
			position: relative;

			.disable-editor {
				border-radius: 8px;
				height: 99%;
				width: 99%;
				position: absolute;
				cursor: not-allowed;
				background: $disable-overlay-bg;
				z-index: 2;
				opacity: 0.5;
			}

			.editor-spin-loader {
				position: absolute;
				top: 50%;
				left: 50%;
				z-index: 3;
				@include flexbox;
				@include align-items(center);
				.loader-text {
					padding-left: 10px;
				}
			}

			.insert-quoted-text-wrapper {
				background: $white;
				padding: 0  0 15px 20px;
				.show-quoted-text,
				.remove-quoted-text  {
					fill: $app-grey;
				}
				.show-quoted-text {
					padding: 0 7px;
					width: 28px;
					border-radius: 10px;
					background: $app-border-primary;
				}
				.remove-quoted-text {
					position: relative;
					top: 2px;
				}
				.remove-quoted-text-btn:hover {
					.remove-quoted-text {
						border-radius: 50%;
						background: $app-border-primary;
					}
				}
				.insert-quoted-text-btn,
				.remove-quoted-text-btn {
					border: 1px solid transparent;
					background: $white;
					padding: 2px;
				}
			}

		}
	}

	.ticket-conversation-card .ticket-note-editor{
		.change-action {
			padding-right: 0;
		}
	}

	.ticket-conversation-card {
		margin-top: 24px;
		&:first-of-type { margin-top: 16px; }
		&:last-of-type { margin-bottom: -8px; }
	}

	.bcc-field-wrapper,
	.cc-field-wrapper,
	.editor-header,
	.to-field-wrapper {
		border-bottom: 1px solid $border-dark-color;
		padding: 7px 20px;
		min-height: 28px;
	}

	.editor-header {
		@include flexbox;
		@include align-items(center);
		padding: 7px 10px !important;
	}

	.convo-type {
		margin: 0 10px;
	}

	.change-action,
	.change-note-type {
		margin: 0 8px 0;
		padding-right: 8px;
	}

	.change-action,
	.editotActionType {
		height: 16px;
		&.show-border {
			border-right: 1px solid $app-border-primary;
		}
	}

	.change-note-type {
		max-width: 240px;

		.ember-power-select-trigger {
			border: 1px solid transparent;
			background: $background-color-2;

			padding-right: 0;

			&.ember-basic-dropdown-trigger--below,
			&.ember-basic-dropdown-trigger--above,
			&:focus {
				border: 1px solid transparent !important;
				box-shadow: none !important;
			}

			&:focus-visible {
				border-color: $inputFocusBorder;
			}

			.ember-power-select-status-icon {
				display: none;
			}

			.ember-power-select-selected-item {
				border: 1px solid transparent;
				background: $background-color-2;
				margin: 0;
				padding: 0;

				.note-type-icon-selected {
					.ember-power-select-status-icon {
						margin-left: 10px;
					}
				}
				.note-type-label {
					padding-right: 20px;
				}

				.ember-power-select-status-icon {
					display: inline-block;
					margin-left: 10px;
				}
			}
		}
	}

	.note-type-icon-selected {
		display: inline-block;
		margin-right: 10px;
		vertical-align: sub;
		border-radius: 4px;

		&.private-note {
			background: $app-warning-state;

			svg {
				padding-bottom: 2px;
			}
		}

		&.public-note {
			background: $tab-icon-color;
		}

		svg {
			margin: 3px;
			display: block
		}
	}

	.note-type-dropdown,
	.ticket-editor-dropdown {
		.change-action-dropdown {
			border: 1px solid transparent;
			background: $background-color-2;
			@include flexbox;
			@include align-items(center);
		}

		.dropdown-down-arrow {
			fill: $app-icon-color;
			margin-left: 5px;
		}

		.ember-basic-dropdown-content {
			min-width: 140px;
			margin-top: 5px;

			li {
				margin: 5px 0;

				.change-action-dd-icon,
				.change-note-type-dd-icon {
					margin-right: 10px;

					&.change-note-type-dd-icon {
						margin-right: 3px;
						vertical-align: middle;
					}
				}

				.change-action-btn {
					background: $white;
					border-color: 1px solid transparent;
					width: 100%;
					text-align: left;
				}
			}
		}
	}

	.note-type-dropdown .ember-basic-dropdown-content {
		width: 230px;

		.note-option-title {
			color: $secondary-text-color-1;
			font-size: $text-normal;
			padding: 5px 0 0 10px;
			display: inline-block;
		}

		.ember-power-select-options {
			padding: 0;

			.ember-power-select-option {
				margin: 8px;
				svg path{
					fill: $app-icon-color;
				}

				.note-type-description,
				.note-type-label {
					display: inline-block;
					font-size: $text-normal;

					&.note-type-label {
						font-weight: $text-weight-semi-bold;
					}

					&.note-type-description {
						padding-left: 20px;
						font-weight: $text-weight-normal;
					}
				}

				&[aria-selected='true'] {
					.note-type-description,
					.note-type-label {
						color: $linkColor;
					}

					&::before {
						top: 30%;
					}
				}
			}
		}
	}

	.from-field-wrapper {
		width: 80%;
		margin-left: 8px;
		@include flexbox;
		@include align-items(center);

		.from-field {
			max-width: 80%;
			margin-left: 5px;

			.ember-basic-dropdown-trigger {
				border: 1px solid transparent;
				background: $background-color-2;
				.ember-power-select-status-icon {
					top: -3px;
				}
			}

			.ember-power-select-selected-item {
				@extend .ellipsis;
				margin-left: 0;
				margin-right: 24px;
			}
		}
	}

	.to-field-wrapper.reply-editor.readonly {
		padding: 11px 20px;

		.to-label {
			float: left;
			margin-left: 52px;
			position: relative;
			top: 5px;
		}

		.to-field {
			width: 65%;
			float: left;
			margin-left: 10px;
			font-weight: $text-weight-medium;
			position: relative;
			top: 5px;
			color: $app-grey;
		}

		.cc-bcc-btns {
			float: right;
			margin-top: 4px;

			.bcc-btn,
			.cc-btn {
				margin-left: 5px;
				padding-right: 0;
			}
		}
	}

	.bcc-field-wrapper,
	.cc-field-wrapper,
	.to-field-wrapper.forward-editor,
	.to-field-wrapper.note-editor,
	.to-field-wrapper.reply-editor.editable-to-field {
		background: $white;

		.bcc-label,
		.cc-label,
		.to-label {
			float: left;
			margin: 8px 10px 0 52px;
			.form-field--errors {
				margin: 0px;
			}
		}

		.bcc-field,
		.cc-field,
		.to-field {
			width: 65%;
			float: left;
		}

		.ember-basic-dropdown-trigger {
			border: 2px solid transparent;

			&.ember-power-select-trigger--active {
				border: 2px solid $inputFocusBorder !important;
				box-shadow: none !important;
			}

			.ember-power-select-status-icon {
				display: none;
			}

			.ember-power-select-multiple-option {
				background: $border-dark-color;
				color: $app-grey;
				font-weight: $text-weight-normal;
				padding-left: 8px;
				padding-right: 2px;

				.ember-power-select-multiple-remove-btn {
					color: $secondary-text-color-1;
				}
			}

			.ember-power-select-multiple-option-error {
				background: $alertWarningBackground;
				border: 1px solid $app-red;
				padding: 1px 2px 1px 8px;
				.ember-power-select-multiple-remove-btn {
					color: $app-red;
				}
			}
		}

		.ember-basic-dropdown-content {
			z-index: 10 !important;
			&.ember-basic-dropdown-content--above {
				top: -55px !important;
			}
		}

		.cc-bcc-btns {
			float: right;
			margin-top: 10px;

			.bcc-btn,
			.cc-btn {
				margin-left: 5px;
			}
		}

		.clear-bcc,
		.clear-cc {
			float: right;
			margin-top: 5px;
			padding-right: 0;
		}
	}

	.editor-footer {
		border-top: 1px solid $border-dark-color;
		padding: 16px 20px;
	}

	.editor-footer-actions {
		@include flexbox-simple(space-between, end);
		margin-left: -7px;

		.ticket-editor-buttons {
			.reply-draft {
				display: inline-block;
				color: $disabled-text-color;
				margin-right: 10px;
				vertical-align: middle;
			}
			.reply-cancel {
				border: 1px solid $btnBorder;
				border-radius: 4px;
				background: $btnIconBackground;
				padding: 4px 6px;
				margin-right: 10px;
				vertical-align: middle;
				width: 32px;
				height: 32px;
				svg {
					width: 14px;
					height: 14px;
					position: relative;
					top: 2px;
					fill: $app-icon-color;
				}
			}
			.btn-group {
				display: inline-block;
				font-size: $baseFontSize;
				.btn-send {
					position: relative;
					float: left;
					margin-right: 0;
				}
				.send-and-set-status-dropdown {
					.send-dropdown {
						border-radius: 0px 4px 4px 0px;
						margin-right: 0 !important;
						width: 32px;
						height: 32px;
						padding: 4px 10px;
						&:focus {
							box-shadow: none;
						}
						svg {
							fill: $white;
							position: relative;
							top: 2px;
							width: 12px;
							height: 12px;
						}
					}
					float: left;
					ul {
						text-align: justify;
						.send-and-set-item {
							text-align: left;
							width: 100%;
							margin: 2px;

							&:focus-visible {
								box-shadow: 0 0 0 2px $linkColor;
								outline: 0;
								width: -webkit-fill-available;
							}
						}
					}
					.ember-basic-dropdown-content {
						margin-top: -5px;
						&.ember-basic-dropdown-content--above.ember-basic-dropdown--transitioning-in {
							animation: drop-fade-below .15s;
						}
						&.ember-basic-dropdown-content--above.ember-basic-dropdown--transitioning-out {
							animation: drop-fade-below .15s reverse;
						}
					}
				}
				.btn-send[disabled],
				.send-dropdown[disabled] {
					background: $btnDisabledGradientColor;
					border: 1px solid $btnPrimaryBorder;
					// pointer-events: none;
					svg {
						pointer-events: none;
					}
				}
			}
		}
		.ticket-editor-actions {
			@include flexbox-simple(center, center);
			.ticket-editor-footer-action,
			.marketplace-app-container {
				.ticket-editor-footer-btn,
				.attach-files-btn,
				.mkp-iframe-btn {
					background: none;
					border: 1px solid transparent;
					width: 32px;
					height: 32px;
					&:hover {
						background: $white;
						border: 1px solid $border-light-color;
						width: 32px;
						height: 32px;
						border-radius: 4px;
					}
					svg {
						fill: $app-icon-color;
						path {
							fill: $app-icon-color;
						}
					}
					&.mkp-iframe-btn img {
						vertical-align: text-bottom;
					}
				}
			}
			.attach-files-container {
				margin: 0px;
				padding: 0px;
				.attachment-uploader-dropdown {
					min-width: auto;
				}
				.attachment-uploader {
					margin-bottom: 0 !important;
					@include flexbox-simple(center, center);
					height: 32px !important;
					width: 32px;
					border: 1px solid transparent;
					.attach-files-btn {
						height: 32px !important;
						width: 32px;
					}
					.attachment-title svg{
						margin-top: 3px;
					}
					&:hover {
						background: $white;
						border: 1px solid $border-light-color;
						width: 32px;
						height: 32px;
						border-radius: 4px;
					}
				}
				.attach-files-btn, .attachment-title  {
					padding: 0;
					justify-content: center;
					svg path{
						fill: $app-icon-color;
					}
				}
				svg {
					width: 14px;
					height: 14px;
					margin-right: 0 !important;
				}
				.ember-basic-dropdown-content {
					width: max-content;
				}
			}
			.canned-response-btn {
				.insert-response {
					padding-top: 8px;
				}
			}
			.insert-solution-btn {
				.insert-solution-icon {
					vertical-align: sub;
				}
			}
		}
	}

	.editor-footer-checkboxes {
		clear: both;
		padding-top: 10px;
		display: flex;

		.related-tkts-chkbox,
		.survey-monkey-chkbox {
			input {
				border-color: $inputHoverBorder;
			}
		}

		.ticket-convo-checkbox-label {
			vertical-align: middle;
		}
		.ticket-convo-checkbox-input {
			border-color: $checkboxDefaultBorder;
			&:not(:checked) {
				background-color: transparent;
			}
		}

		.vertical-separator {
			height: 24px;
			margin: 0 16px;
			border-right: 1px solid $app-border-primary;
			vertical-align: middle;
		}
	}

	.tkt-forward-form,
	.tkt-note-form,
	.tkt-reply-form {
		.form-field--label {
			display: none !important;
		}

		.froala-editor-container {
			border: 0;
			z-index: 0;

			.fr-toolbar {
				border: none;
				box-shadow: none;
				background-color: $white;
				border-radius: 0;

				svg.fr-svg {
					height: 18px;
					path {
						fill: #475867;
					}
				}
			}

			.fr-wrapper {
				border: 0;
				height: 332px !important;
				.fr-element {
					min-height: 332px !important;
				}
			}

			.fr-second-toolbar {
				display: none;
			}
		}
	}

	.tkt-editor-attachment-list-container {
		background: $white;
		padding: 16px 20px;
		.tkt-editor-attachment-list {
			background: $white;
			box-sizing: border-box;
			border-radius: 8px;
			border: 0;
			margin: 0 !important;
			&.showBorder {
				border: 1px solid $app-border-primary;
			}
		}
	}

	.svg-14 {
		width: 14px;
		height: 14px;
	}

	button {
		font-family: $baseFontFamily;
	}
}
