.approval-option-style {
	background-color: transparent;
	border: none;
	text-align: left;
	border-radius: 4px;
	width: 100%;
}

#ticket-approval-tab {
	font-family: $baseFontFamily;
	.add-approval-header {
		margin-top: -3px;
		color: $inputFocusBorder;
		float: right;
		.btn-link {
			@include flexbox-simple(center, center);
			border: none;
			padding-top: 2px;
			padding-right: 0px;
			font-family: $baseFontFamily;
			font-weight: $text-weight-medium;
		}
		.add-icon {
			height: 16px;
			vertical-align: text-top;
			font-family: $baseFontFamily;
		}
		.add-text {
			padding-left: 5px;
			font-family: $baseFontFamily;
		}
	}
	.approval-list-container {
		width: calc(100% + 10px);
		.approval-list {
			padding-bottom: 25px;
			width: calc(100% - 10px);

			.approval-level-container {
				.approval-level-header {
					color: $secondary-text-color-1;
					padding-bottom: 16px;
					font-weight: $text-weight-medium;
				}

				.approval-item {
					padding: 12px 16px;
					min-height: 80px;
					background: $white;
					margin-bottom: 12px;
					box-sizing: border-box;
					box-shadow: $list-cards-box-shadow;
					@include border-style($border-light-color, 8px);
					overflow: visible;

					.approver-details {
						@include flexbox-simple(flex-start, flex-start);

						.approver-name {
							padding-left: 10px;
							.approver-btn {
								border: none;
								background: none;
								max-width: 250px;
								font-size: $baseFontSize;
								font-family: $baseFontFamily;
								color: $primary-text-color-1;
								font-weight: $text-weight-semi-bold;
							}
							.approver-btn:hover {
								color: $inputFocusBorder;
							}
						}
					}
					.approval-section {
						margin-top: -13px;
						margin-left: 48px;
						font-size: $text-normal;
						color: $secondary-text-color-1;

						.approval-status {
							@include flexbox-simple(space-between, center);
							min-height: 35px;

							.approval-status-details {
								@include flexbox-simple(normal, normal);
								.approval-svg {
									padding-top: 3px;
								}
								.status-text {
									padding-left: 5px;
									.delegatee,
									.delegator {
										.approver-btn {
											padding: 0;
											border: none;
											background: none;
											max-width: 250px;
											font-size: $text-normal;
											color: $primary-text-color-1;
											font-weight: $text-weight-semi-bold;
										}
										.approver-btn:hover {
											color: $inputFocusBorder;
										}
									}
								}
							}
							.approval-last-reminder {
								.placeholder {
									text-align: right;
									color: $app-success-state1;
								}
								.sent-date-time {
									color: $app-form-label-color;
								}
							}
							.approval-message {
								margin-top: -5px;
								padding-left: 12px;
								@include flexbox-simple(normal, center);
								.view-hide-message {
									margin-left: 3px;
								}
								.message-text {
									color: $app-primary;
									font-size: $text-normal;
									font-family: $baseFontFamily;
									font-weight: $text-weight-medium;
								}
							}
							.approval-action {
								display: flex;
								align-items: center;
								.btn {
									display: flex;
									align-items: center;
									background: $white;
								}
								.btn:hover {
									background: $background-color-2;
								}
								.action-text {
									padding: 6px;
								}
								.reject-btn {
									margin-right: 0px;
								}
							}
							.approval-option-menu {
								width: 32px;
								height: 32px;
								color: $app-grey;
								margin: -3px 8px 0 14px;
								font-weight: normal;
								.ember-basic-dropdown-content {
									width: 120px;
									li {
										padding: 4px;
										border-radius: 4px;
										&:hover {
											cursor: pointer;
											background: $dropdown-hover;
										}
									}
								}
								.approval-more-btn {
									margin-top: 2px;
									padding: 4px 8px;
									line-height: 24px;
									background: $white;
									svg {
										width: 14px;
										height: 12px;
										fill: $app-grey;
									}
									&:hover {
										background: $background-color-2;
									}
								}
								.approval-dropdown-menu {
									padding: 4px;
									width: 198px !important;
									> .approval-option-item {
										padding: 7px 12px;
										margin: 2px 0;
										font-weight: $text-weight-normal;
										font-size: $title-small;
										color: $primary-text-color-1;
										@extend .approval-option-style;
										&:hover {
											background: $border-light-color;
										}
									}
								}
							}
						}
						.remark-item {
							padding: 5px 8px;
							max-width: 90%;
							margin-top: -5px;
							margin-left: 2.5%;
							margin-bottom: 3px;
							border-radius: 8px;
							width: max-content;
							overflow-wrap: break-word;
							border-top-left-radius: 0;
							background: $background-color-2;
						}
						.approval-item-right-container {
							display: flex;
							align-items: center;
							margin-top: -25px;
						}
						.approval-content-container {
							border-bottom-left-radius: 8px;
							border-bottom-right-radius: 8px;
							margin: 12px -17px -13px -65px;
							box-sizing: border-box;
							background: $background-color-2;
							border: 1px solid $border-light-color;
							.approval-content {
								padding: 22px 17px;
								color: $app-grey;
								font-weight: normal;
								font-size: $baseFontSize;
							}
						}
					}
				}
			}
		}
	}
}

.approval-modal {
	.ember-modal-footer {
		.btn {
			min-width: 80px;
		}
	}
}
