#task-show {
	max-width: 514px;
	overflow-wrap: break-word;
	padding: 8px 0px;
	
	.task-title {
		font-size: 16px;
		font-weight: $text-weight-semi-bold;
		color: $primary-text-color-1;
	}

	.note {
		padding: 24px 22px 15px 0px;
		color: $primary-text-color-1;
		font-size: 14px;
		font-weight: $text-weight-normal;
		.content > * {
			white-space: normal !important;
		}
		label {
			color: $svg-icon-color;
			font-size: 12px;
			font-weight: $text-weight-semi-bold;
		}
	}

	.more-details {
		margin-left: 16px;
		.detail-item-row {
			display: flex;
			margin-bottom: 20px;
			.svg-wrapper {
				height: 36px;
				display: flex;
				align-items: center;
				justify-content: center;
				padding-right: 10px;
			}
			svg {
				width: 20px;
				height: 20px;
				&.group-icon {
					fill: $tab-icon-color;
				}
				&.status-icon {
					fill: $app-warning-state;
				}
				&.status-icon-green {
					fill: $app-success-state;
				}
				&.notify-icon > path {
					fill: $app-success-state;
				}
				&.date-icon > path {
					fill: $linkColor;
				}
			}
			.detail-item-col {
				margin-left: 15px;
				display: flex;
				flex-direction: column;
				label {
					color: $secondary-text-color-1;
					font-size: 12px;
					font-weight: $text-weight-normal;
				}
				.content {
					word-break: break-word;
				}
			}
		}
	}
}