.ticket-state-badge {
	.ember-badge {
		font-weight: $text-weight-medium;
		line-height: 1.3;
		vertical-align: middle;
		border-radius: 2px;

		&.state-new {
			@extend .ember-badge-success;
		}

		&.state-customer_responded {
			@extend .ember-badge-blue2;
		}

		&.state-overdue,
		&.state-elapsed {
			@extend .ember-badge-error;
		}

		&.state-customer_responded_overdue {
			@extend .ember-badge-blue2;
			&:before {
				content: " ";
				background: lighten($app-red-1, 10%);
				height: 10px;
				width: 10px;
				border-radius: 100%;
				position: absolute;
				margin-left: -5px;
				margin-top: 2px;
				box-shadow: inset 0px 0px 0px 1px $app-white;
			}
			span.value, span.state-value {
				padding-left: 12px;
			}
			&:hover,
			&:focus {
				&:before {
					box-shadow: inset 0px 0px 0px 1px $app-black;
				}
			}
		}
	}
}
