$notification-dot: #E35457;

@mixin ellipsis-dot ($dot-height, $dot-width, $dot-color){
	height: $dot-height;
	width: $dot-width;
	border-radius: 50%;
	display: inline-block;
	background-color: $dot-color
}

.collab-unread-indicator {
	@include ellipsis-dot(8px, 8px, $notification-dot);
	position: absolute;
	margin-top: -6px;
	margin-left: 9px;
}