.fulfill-asset-modal {
	.ember-modal-body {
		overflow-x: hidden;
	}
	
	h2 {
		font-weight: $text-weight-semi-bold;
		font-size: $title-extra-large;
	}

	.back-btn {
		font-weight: $text-weight-semi-bold;
		align-items: center;
		svg { fill:  $app-primary; }
	}

	.loader-container {
		height:750px;
		svg {
			fill: $app-primary;
			width: 25px;
			height: 25px;
		}
	}

	.fulfill-request-search-box {
		.dropdown-search {
			position: relative;
			left: 8px;
			top: 30px;
			fill: $primary-text-color-1;
		}
	
		input  {
			width: 535px;
			padding-left: 32px;
			margin-bottom: 20px;
		}
	}

	.search-result-help-text {
		margin-bottom: 15px;
		b {
			font-weight: bold;
		}
	}

	.asset-box {
		position: relative;
		margin-bottom: 10px;

		.asset-checkbox-cell {
			position: absolute;
			top: 24px;
			left: 15px;
		}
	
		.fulfill-asset-card  {
			
			.asset-delete-icon {
				position: absolute;
				top: 20px;
				left: 10px;
			}
		
			.asset-card {
				width: 535px;
				height: 70px;
				background:	$app-white;
				border: 1px solid $border-light-color;
				box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
				border-radius: 8px;
	
				&:hover {
					border-color: $border-light-blue !important;
				}
				
				.fulfill-asset-card-container {
					position: relative;
					left: 30px;
					padding: 10px;
				}
				
				.asset-icon-box {
					width: 32px;
					height: 32px;
					background: $section-background-blue;
					border: 1px solid $border-light-blue;
					border-radius: 4px;
					align-self: center;
				}
	
				.asset-icon {
					fill: $inputFocusBorder;
					position: relative;
					top: 7px;
					left: 7px;
				}
	
				.asset-item {
					flex: 1;
					flex-direction: column;
					margin-left: 15px;
	
					.asset-title {
						color: $inputFocusBorder;
						font-weight: $text-weight-medium;
						max-width: 400px;
						display: inline-block;
					}
	
					a {
						width: 420px;
						height: 20px;
						svg {
							fill: $inputFocusBorder;
							display: none;
							vertical-align: text-top;
						}
						&:hover {
							svg { display: inline-block;	}
						}
					}
	
					.asset-secondary-title {
						font-weight: $text-weight-normal;
						font-size: $text-normal;
						color: $secondary-text-color-1;
						margin: 8px 0 0 0;
						height: 20px;
	
						.with-dot-separator::after{
							content: "";
							background: $border-dark-color;
							border-radius: 50px;
							width: 4px;
							height: 4px;
							margin: 0 6px 2px 6px;
							display: inline-block;
						}
					}
				
					.ellipsis-item-wrap {
						max-width: 70px;
						display: inline-block;
						font-weight: $text-weight-medium;
						color: $primary-text-color-1;
						vertical-align: bottom;
					}
				}
			}

			&.consumable-item {
				.asset-delete-icon { top: 25px; }
				.asset-card {
					height: 85px;

					.asset-item { 
						display: block; 
						.asset-secondary-title {
							margin: 0;
							.location-text.with-dot-separator {
								&::after {
									display: none;
								}
							}
							.asset-tag-text {
								display: block; 
							}
						}
					}
					
					.qty-input-field {
						width: 150px;
						font-size: $text-normal;
						span {	color: $secondary-text-color-1;	}
						input { 
							width: 50px; 
							&:disabled { background: $background-color-2; }
						}
						input, .qty-text {
							position: relative;
							top: 8px;
						}
						.qty-text {
							font-weight: $text-weight-bold;
						}
					}
				}
			}
		}
	}

	.view-item-fulfill-list {
		margin-top: 20px;
		.fulfill-asset-card-container {
			left: 35px !important;
		}
	}

	.empty-state {
		img {
			min-height: 140px;
		}
	}

	.fulfill-request-pagination {
		justify-content: center;
		.ember-pagination-new {
			border: none;
			.pagination__link--disabled {
				color: rgba(71, 88, 103, 0.5) !important;
			}
		}
	}

	.fulfill-asset-footer {
		.fulfill-asset-footer-left-content {
			flex: 1;
			font-weight: $text-weight-medium;
			a {	margin-left: 10px; }
		}
	}
}