.add-req-item-modal.ember-modal-dialog  {
	#add-item-modal-header {
		font-weight: $text-weight-semi-bold;
		font-size: $title-extra-large;
		line-height: 29px;
	}

	#category-dropdown-container {
		display: flex;
		height: 32px;
		align-items: center;
		.category-wrapper .ember-basic-dropdown-content {
			width: 205px;
			max-height: 500px;
			overflow-y: auto;
		}
	}

	.category-loader.shimmer-loader {
		height: auto;
		min-width: 200px;
		.shimmer-loader-item {
			margin: 1px;
		}
	}

	.category-list {
		display: flex;
		align-items: center;
		font-weight: $text-weight-semi-bold;
		cursor: pointer;
		max-width: 200px;
		.dd-container {
			display: flex;
		}
	}

	.dd-isOpen {
		transform: rotate(180deg);
	}

	#catalog-items-list .shimmer-loader {
		padding-top: 12px;
	}

	.search-container {
		right: 0;
		top: 0;
		position: absolute;
		.search-input-wrapper {
			@extend .flex-center;
			position: relative;
			svg {
				position: absolute;
				left: 8px;
				fill: $secondary-text-color-1;
			}
			.search-items {
				padding: 6px 32px;
				width: 320px;
			}
			.search-clear {
				top: 1px;
				width: 30px;
				height: 30px;
				right: 3px;
				position: absolute;
				border: 0;
				svg {
					left: 0;
					position: relative;
					width: 26px;
					height: 26px;
				}
			}
			.shimmer-loader-item-fast {
				height: 32px;
				margin: 0;
				width: 320px;
			}
		}
	}
	&.ember-modal-dialog.ember-modal-side .ember-modal-body {
		height: calc(100% - 71px);
		&.add-item-list {
			padding-bottom: 0;
			padding-right: 0;
			overflow-y: hidden;
			
			&.add-or-associate-child-tickets  {
				.toggle-buttons {
					padding-bottom: 12px;
				}
				.existing-tickets {
					display: flex;
					flex-direction: column;
					height: 80vh;

					.search-section {
						padding-top: 0;
					}

					.search-results {
						flex: 1 1 auto;
						overflow-y: auto;
					}
				}
			}
		}
	}

	.catalog-items-empty {
		margin: 50px auto;
	}

	#other-results {
		font-size: $title-small;
		margin-bottom: 24px;
	}
	
	.ember-modal-footer {
		padding: 0 32px;
		justify-content: space-between;
		#add-item-footer  .btn-primary {
			margin-right: 0;
		}
		#add-item-qty-details .catalog-item-sidebar-scrollable {
			position: absolute;
			left: 0;
			bottom: 56px;
			height: 100%;
		}
		&.add-item-list-footer {
			display: none;
			&.add-child-sr-list-footer {
				display: flex;
				justify-content: flex-end;
			}
		}
	}

	.loaner-fields-wrapper {
		flex-direction: column;
		align-items: flex-start !important;
		.slots-list {
			left: 0 !important;
		}
	}

	.add-or-associate-child-tickets .toggle-buttons {
		padding: 0 0 30px;
	}

	.add-item-in-slider {
		padding-bottom: 100px;

		.accordion-list {
			padding-bottom: 250px;
		}
	}
}

@import "item-list";
@import "item-detail";
@import "additional-items";
@import "../../tickets/child-tickets";

.list-items-in-slider	{
	position: relative;
	height: 100%;
	.shimmer-loader-card.split-card-with-logo {
		margin: 12px 0;
	}
	.search-list-container {
		height: calc(100% - 56px);
		#catalog-items-list {
			height: 95%;
			overflow-y: auto;
			margin: 24px 0;
			.list-catalog-items {
				margin: 0 32px 24px 0;
				display: block;
			
				.catalog-item-card {
					margin-bottom: 8px;
					min-height: 80px;
					height: auto;
					max-height: 100px;
					cursor: pointer;
					overflow-x: auto;
			
					img.item-icon {
						width: 40px;
						max-height: 40px;
						overflow: hidden;
					}
			
					.info-container {
						display: flex;
						justify-content: space-between;
						width: 90%;
			
						.main-info {
							width: 75%
						}
			
						.item-desc {
							margin: 4px 0 0;
						}
			
						.item-attributes-info {
							align-items: center;
							padding-left: 5px;
						}
					}
				}
			}
		}
		.search-container {
			padding-right: 32px;
		}
	}


	@import 'list-empty-state';
} 
