td.qty-btns {
	height: 44px;
}

.inp-qty {
	max-width: 3em;
	min-width: 2em;
	padding: 4px;
	text-align: center;
	margin: 0;
	border-radius: 0;
	height: 28px;

	&::-webkit-outer-spin-button,
	&::-webkit-inner-spin-button {
		-webkit-appearance: none;
		margin: 0;
	}

	-moz-appearance: textfield;
}

.qty-btn-container {
	display: flex;

	.btn:focus,
	.inp-qty:focus {
		z-index: 2;
		border: inherit;
	}

	.inp-qty:focus {
		border: inherit;
	}

	.btn {
		margin: 0;
		padding: 4px;
		width: 28px;
		height: 28px;
		background: $background-color-2;
		display: flex;
		align-items: center;
		justify-content: center;
		min-width: 28px;

		&:not([disabled]) {
			background: $background-color-2;
		}

		&[disabled] {
			cursor: not-allowed;
		}

		&:focus {
			border: 1px solid $border-dark-color;
		}

		&.minus {
			border-top-right-radius: 0;
			border-bottom-right-radius: 0;
			border-right: 0;
		}

		&.plus {
			border-top-left-radius: 0;
			border-bottom-left-radius: 0;
			border-left: 0;
		}

		svg {
			width: 8px;
			height: 8px;
			fill: $secondary-text-color-1;
		}

		&:hover {
			svg {
				fill: $app-icon-color;
			}
			background: $background-color;
		}
	}

	.svg-cross {
		align-self: center;
		margin-left: 8px;
		fill: $secondary-text-color-2;
		width: 15px;
		min-width: 15px;
	}
}