.kanban-page-wrapper {
	height: calc(100vh - 65px);
	overflow: hidden;

	.page-sidebar {
		z-index: 9;
		position: absolute;
		right: 0;
	}

	.kanban-board-wrapper {
		width: 100%;
		background-color: $app-white;
		overflow: hidden;
	}

	.kanban-board--properties {
		justify-content: space-between;
		.kanban-board-left-properties{
			top: 3px;
			position: relative;
		}
	}

	.kanban-board--properties-visibility {
		@extend .flex-center;
		font-size: $text-normal;
		font-weight: $text-weight-semi-bold;
		.visible-label{
			font-weight: $text-weight-medium;
			color: $secondary-text-color-2;
			margin: 0 6px;
		}
	}

	.kanban-board--properties-divider {
		border-right: 1px solid $border-light-color;
		height: 24px;
		margin: 0 8px;
	}

	.kanban-board--properties-visibility-icon {
		width: 16px;
	}

	.sort-by-container{
		line-height: 20px;
		.kanban-board--properties-prop-name {
			color: $app-grey;
		}

		.kanban-board--properties-prop-value {
			color: $app-grey;
			font-weight: $text-weight-semi-bold;
			margin-left: 3px;
			height: 20px;
			@extend .flex-center;
			.board-sort-order {
				.ember-basic-dropdown-trigger > div{
					display: flex;
					&.custom-sort-true{
						cursor: pointer;
						.sort-name{
							margin-top: 3px;
						}
					}
					.sort-dd-arrow{
						margin-left: 4px;
						svg{
							margin-top: 6px;
						}
					}
				}
				.ember-basic-dropdown-content{
					width: 210px;
					padding: 12px 8px 12px 9px;
					li{
						padding: 6px 14px 5px 14px;
						font-weight: $text-weight-medium;
						&:hover{
							background-color: $dropdownHover;
						}
					}
				}
			}
		}
	}

	.kanban-board--properties-filter {
		padding-right: 32px;
		height: 32px;
		.btn {
			box-shadow: none;
			margin: 1px 1px 0;
			padding: 7px 0 0 0;
			z-index: 9;
			width: 32px;
			position: fixed;
			background: $app-white;
			&:hover{
				background: $background-color-2;
			}
			&:focus {
				outline: none;
				box-shadow: 0 0 0 1px $inputFocusBorder !important;
			}
			&:active,
			&.active {
				background: $section-background-blue;
				border: 1px solid $inputFocusBorder !important;
				border-radius: $inputBorderRadius;
				box-shadow: none;
				svg path {
					fill: $inputFocusBorder;
				}
			}
		}
	}

	.kanban-board--column-wrapper {
		.kanban-board--column {
			background: $background-color-2;
			border-radius: $inputBorderRadius;
			height: 100%;
			flex-shrink: 0;
			padding-bottom: 8px;
			width: 275px;
			position: relative;

			&.kanban-board--column--limit-exceeded {
				background-color: $app-casablanca-light;
				.kanban-board--column-count {
					background-color: $light-red;
					border: 1px solid $light-red;
					color: $app-white;
					cursor: pointer;
					font-weight: $text-weight-semi-bold;
				}
			}
			.kanban-board-column-body {
				max-height: calc(100vh - 193px);
				overflow-y: auto;
				.sort-pane{
					min-height: 100px;
					&.empty-column {
						min-height: 116px;
					}
				}
				.kanban-board--column-empty-text {
					text-align: center;
					position: absolute;
					width: 100%;
					top: 50%;
					color: $secondary-text-color-2;
				}
			}
			.kanban-board--column-loader {
				@extend .flex-center;
				flex-direction: column;
				min-height: 130px;
				font-size: $text-normal;

				.ember-loader  {
					width: 40px;
					height: auto;
					fill: $app-primary
				}
			}
			&:not(:last-child){
				margin-right: 16px;
			}
		}
		padding-top: 20px;
		overflow-x: auto;
		min-height: calc(100vh - 132px);
		width: 100%;
		box-sizing: border-box;

		&.disabled {
			pointer-events: auto;
			color: inherit;
		}

		> .kanban-board--column-sortable {
			&:first-of-type {
				margin-left: 0;
			}
		}
	}

	.sortable-placeholder {
		flex-shrink: 0;
		background: url('/a/assets/images/module-dashboard/placeholder-pattern.svg') repeat;
		box-sizing: border-box;
		z-index: 9;
		opacity: 0.2;
		position: relative;
	}
}

.kanban-board--column-load-more {
	@extend .flex-center;
	margin-bottom: 24px;

	.ember-loader  {
		width: 28px;
		height: auto;
		fill: $app-primary;
	}
}

.kanban-board--column-props {
	color: $app-grey;
	font-size: $title-small;
	font-weight: $text-weight-medium;
	padding: 12px 8px 8px;
	justify-content: space-between;
}

.kanban-board--column-props-more {
	display: flex;
	@extend .flex-center;
	height: 24px;
	padding: 0;
	box-shadow: none;
	margin-right: 0;
	background: $app-white;
	&:hover{
		background: $background-color-2;
	}
	svg {
		overflow: visible;
		margin: 5px;
	}
}

.kanban-board--column-name {
	display: inline-block;
	max-width: 190px;
	vertical-align: bottom;
	@extend .ellipsis;
}

.kanban-board--column-count {
	background-color: $app-white;
	border: 1px solid $border-light-color;
	box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
	border-radius: 20px;
	padding: 0 8px;
	font-size: $text-normal;
	height: 18px;
	display: inline-flex;
	align-items: center;
}

.kanban-board--column-count-tooltip {
	font-size: $text-normal;
}

.kanban-column-options .ember-basic-dropdown-content {
	width: max-content;
	z-index: 1;
}

.ember-basic-dropdown-content .kanban-views-basic-dropdown {
	.basic-dropdown-item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: $title-small;
		font-weight: $text-weight-normal;
	}
}

.kanban-board--column-props-pos {
	flex-direction: column;
	width: 200px;
	.ember-basic-dropdown-content ul{
		width: 190px;
	}
}

.kanban-board--column-props-pos-navigation {
	@extend .flex-center;
	color: $linkColor;
	font-size: $text-normal;
	margin: 0 0 8px -8px;
	padding: 0;
	height: 26px;
	font-weight: $text-weight-medium;
}

.kanban-board--column-props-pos-navigation-icon {
	fill: currentColor;
	overflow: visible;
}

.kanban-board--column-props-pos-title {
	font-size: $text-normal;
	color: $app-form-label-color;
	font-weight: $text-weight-semi-bold;
	margin-bottom: 8px;
	margin-left: 4px;
}

.kanban-board--column-props-pos-input {
	margin: 0 4px;
}

.kanban-board--column-props-pos-actions-btns {
	justify-content: flex-end;
	background: $background-color-2;
	padding: 12px 0;
	margin: 24px -8px -8px -8px;

	.btn-mini {
		@extend .flex-center;
	}
}

// board create edit model css
.kanban-board-modal {
	&.ember-modal-dialog.ember-modal-center {
		top: 41%;
		.ember-modal-body {
			max-height: none;
			.board-disabled-field {
				margin: 0;
				&:last-child{
					margin-bottom: -16px;
				}
				label{
					padding-bottom: 2px;
				}
				.ember-basic-dropdown {
					.ember-basic-dropdown-trigger {
						border: none !important;
						background-color: $app-white;
						display: flex;
						cursor: default;
						.ember-power-select-selected-item{
							margin-left: 0;
						}
						.ember-power-select-status-icon{
							display: none;
						}
						span{
							line-height: 20px;
							color: $app-grey;
						}
					}
				}
			}
			input{
				margin-top: 4px;
			}
			.radio-field label{
				font-size: $title-small;
				color: $primary-text-color-1;
			}
			.board-visibility{
				margin-bottom: 0;
			}
			.visibility-groups{
				.ember-basic-dropdown{
					margin-left: 18px;
					width: 214px;
					.search-container{
						top: 19px;
					}
				}
				label{
					display: none;
				}
				.form-field--errors div{
					margin-left: 20px;
				}
			}
		}
		.ember-modal-header .title{
			font-weight: $text-weight-semi-bold;
		}
	}
}

.ember-modal-overlay{
	.kanban-board-column-limit-modal, .kanban-board-modal{
		&.ember-modal-dialog{
			.ember-modal-footer{
				width: auto;
				padding: 0 16px;
				button:last-child{
					margin-right: 0;
				}
			}
		}
	}
	.delete-board-modal .ember-modal-body{
		padding-bottom: 32px;
		.delete-board-warning{
			margin: 0;
		}
	}
}

.kanban-views-basic-dropdown, .kanban-basic-dropdown{
	li:not(:last-child){
		margin-bottom: 5px;
	}
}

// board column items limit set model css
.kanban-board-column-limit-modal {
	&.ember-modal-dialog{
		.ember-modal-header{
			padding: 32px 32px 12px;
		}
		.ember-modal-footer {
			button:last-child{
				min-width: 70px;
			}
		}
	}
	.kanban-board-column-limit--input {
		width: 90px;
		padding-right: 12px;
		display: block;
		margin-top: 12px;
	}

	.kanban-board-column-limit--validation-message {
		display: block;
		color: $app-error-state;
		font-size: $text-normal;
		margin-top: 4px;
	}

	.kanban-board-column-limit--info-message {
		color: $secondary-text-color-1;
		margin-top: 10px;
		height: 20px;
		font-size: $text-normal;
		.kanban-board-column-limit--info-icon {
			fill: $secondary-text-color-1;
			margin-right: 3px;
		}
	}
}

// board column card css
.kanban-board--column-card {
	cursor: pointer;
	border: 1px solid $border-light-color;
	background: $app-white;
	box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
	border-radius: $inputBorderRadius;
	margin: 0 8px 8px 8px;
	padding: 0;
	-webkit-user-select: none;
	-ms-user-select: none;
	user-select: none;
	box-sizing: border-box;

	&:hover{
		box-shadow: 0px 1px 10px rgba(18, 52, 77, 0.12);
		border: 1px solid $app-border-primary;
	}

	> div{
		padding: 12px;
	}

	&:focus {
		border: 1px solid $inputFocusBorder;
	}

	&.dragging {
		cursor: grabbing;
		transform: rotate(5deg);
		box-shadow: 0px 1px 10px rgba(18, 52, 77, 0.12);
		border: 1px solid $app-primary;
	}

	.ticket-card--header {
		font-size: $text-normal;
		line-height: 18px;
		color: $app-form-label-color;
		width: 100%;
		height: 18px;
		.card-vip-badge{
			margin-left: auto;
		}
	}

	.ticket-card--header-id {
		max-width: 73px;
	}

	.ticket-card--header-due-in {
		font-weight: $text-weight-medium;
		display: inline-flex;
		max-width: 154px;
		margin-left: 6px;

		&.ticket-card--header-due-in--resolved {
			color: $secondary-text-color-2;
			path {
				fill: $secondary-text-color-2;
			}
		}

		&.ticket-card--header-due-in--overdue {
			color: $app-danger-red;
			path {
				fill: $app-danger-red;
			}
		}
	}

	.ticket-card--header-due-in-icon {
		width: 12px;
		height: auto;
		margin-right: 5px;
		overflow: visible;
	}

	.ticket-card--body {
		color: $app-form-label-color;
		font-size: $title-small;
		margin-top: 8px;
		line-height: 20px;
		word-break: break-word;
		.ember-badge {
			font-size: $text-normal;
			font-weight: $text-weight-semi-bold;
			margin-top: 8px;

			&.state-new {
				@extend .ember-badge-success;
			}

			&.state-customer_responded {
				@extend .ember-badge-blue2;
			}

			&.state-overdue,
			&.state-elapsed {
				@extend .ember-badge-error;
			}

			&.state-customer_responded_overdue {
				position: relative;
				@extend .ember-badge-blue2;

				@mixin box-shadow($color) {
					-moz-box-shadow: inset 0px 0px 0px 1px $color;
					-webkit-box-shadow: inset 0px 0px 0px 1px $color;
					box-shadow: inset 0px 0px 0px 1px $color;
				}

				&:before {
					content: " ";
					background: lighten($app-red-1, 10%);
					height: 10px;
					width: 10px;
					border-radius: 100%;
					position: absolute;
					margin-left: -5px;
					margin-top: 0;
					@include box-shadow($app-white);
				}
				span.value {
					padding-left: 12px;
				}

				&:hover,
				&:focus {
					&:before {
						@include box-shadow($app-black);
					}
				}
			}
		}
	}

	.ticket-card--footer {
		margin-top: 12px;
		justify-content: space-between;
	}

	.kanban-status-dropdown-div{
		margin-left: 8px;
	}

	&.sortable.disabled{
		cursor: pointer;
		pointer-events: all;
		.kanban-status-dropdown-div, .kanban-priority-dropdown-div{
			.priority-list-wrapper, .status-list-wrapper{
				.priority-result, .status-result{
					color: $secondary-text-color-2;
					background: $background-color-2;
					cursor: not-allowed;
				}
			}
		}
		.kanban-assign-to-dropdown {
			span, .avatar__initial, .avatar__icon{
				cursor: not-allowed;
			}
		}
	}

	.kanban-assign-to-dropdown {
		.assign-to-avatar-trigger .agent-group-content{
			margin-left: 8px;
		}
		.shimmer-loader-item {
			margin: 0;
		}
	}

	.kanban-status-dropdown-div, .kanban-priority-dropdown-div{
		.status-list-wrapper, .priority-list-wrapper{
			.ember-basic-dropdown-trigger{
				padding: 0;
				display: flex;
				min-width: auto;
				height: 24px !important;
				margin-top: 0 !important;
				&:hover, &[aria-expanded]{
					box-shadow: none;
					> span{
						border: 1px solid $app-border-primary;
					}
				}
			}
			> span, .ember-basic-dropdown-trigger > span{
				border: 1px solid $background-color;
				border-radius: $inputBorderRadius;
				padding: 1px 8px;
				font-size: $text-normal;
				font-weight: $text-weight-medium;
				color: $app-form-label-color;
				max-width: 90px;

				&:focus {
					border: 1px solid $inputFocusBorder;
				}
			}
			svg{
				display: none;
			}
			.priority-color{
				margin: 6px 7px 0 0;
			}
			.shimmer-loader{
				width: 72px;
				.shimmer-loader-item{
					margin: 0;
				}
			}
		}
	}
}

//Edit Delete dropdown
.kanban-actions-dropdown{
	margin-left: 16px;
	.ember-basic-dropdown-content{
		padding: 9px 11px;
		width: 170px;
		li{
			button.basic-dropdown-item{
				display: flex;
				align-items: center;
				padding-left: 9px;
				border: none;
				background: no-repeat;
				width: 100%;
				box-shadow: none;
				font-weight: $text-weight-normal;
				span{
					max-width: 102px;
				}
				&:hover{
					background: $dropdownHover;
				}
			}
		}
	}
	&.view-actions-dropdown .ember-basic-dropdown-content .basic-dropdown-item .ember-svg{
		margin-right: 13px;
	}
	.kanban-more-options{
		box-shadow: none;
	}
}

/* the below changes should be in sync with
*  public/src/ember/base/lib/_boards-list.scss
*/

#header-page-title-container .burger-menu-container.kanban-board-list {
	&.ember-modal-dialog .ember-modal-body {
		height: 100%;
		margin: 12px;
		padding: 0;
	}
	#board-list-menu-trigger{
		font-size: $title-normal;
		font-weight: $text-weight-semi-bold;

		.view-modified, .view-selected{
			font-weight: $text-weight-normal;
		}
		.view-modified::before{
			content: '*'
		}
	}
	.menu-list-container {
		padding: 12px;
		.menuitem{
			&:hover{
				background: $background-color-2;
			}
			&.view-item{
				display: flex;
				padding: 0 12px 0 0;
				&.new-board-item{
					margin-bottom: 32px;
				}
				.shimmer-loader,.new-board-btn{
					&:after{
						position: absolute;
						content: "";
						border-bottom: 1px solid $border-light-color;
					}
				}
				.shimmer-loader{
					&:after{
						width: 120%;
						right: -25px;
						height: 14px;
					}
				}
				a{
					margin-left: 12px;
					max-width: none;
					font-weight: $text-weight-normal;
				}
				.new-board-btn{
					font-weight: $text-weight-semi-bold;
					color: $linkColor;
					padding-top: 8px;
					display: inline-flex;
					align-items: center;
					svg{
						margin-right: 8px;
					}
					&:after{
						width: 100%;
						height: 61px;
						right: 0;
					}
					&:hover{
						border-radius: $inputBorderRadius;
					}
					&:focus,
					&:hover {
						background: $background-color-2;
						color: $linkColorHover;
					}
				}
				&.active svg{
					left: 0;
					display: block !important;
				}
			}
		}
		.kanban-board-list-loading {
			height: 85%;
			.ember-loader  {
				width: 28px;
				height: auto;
				fill: $app-primary
			}
		}
	}
}