.insert-solution-modal {
	.svg-16 {
		width: 16px;
		height: 16px;
	}
	.insert-solution-header {
		@include flexbox-simple(space-between, center);
		.new-solution-btn {
			@include flexbox;
			@include align-items(center);
			background: $white;
			&:hover {
				background: $background-color-2 !important;
			}
		}
		.new-solution-icon {
			width: 14px;
			height: 14px;
			margin-right: 5px;
		}
		.solution-back-btn {
			font-weight: $text-weight-semi-bold;
			padding: 0;
			position: relative;
			padding-left: 20px;
			.solution-back-icon {
				fill: $linkColor;
				position: absolute;
				left: -5px;
			}
		}
	}
	.solution-filter-search-container {
		@include flexbox;
		.language-selection-dropdown {
			flex: 1;
		}
		.solution-search {
			flex: 2;
			.search-box {
				@include flexbox;
				@include align-items(center);
				@include svg-fill($disabled-text-color);
			}
			.search-icon {
				width: 16px;
				height: 16px;
				position: relative;
				left: 24px;
				fill: $svgFocusedBorder;
			}
			.search-text-box {
				padding-left: 30px;
			}
			&.language-filter-hidden {
				margin-left: -15px
			}
		}
	}

	.empty-solution-search-result {
		text-align: center;
		color: $secondary-text-color-1;
		.search-hint {
			margin-top: 16px;
		}
	}

	.insert-soln-list-footer {
		display: none !important;
	}
}
