@import "../../tickets/add-cc";

.request-item-detail, .add-req-item-modal {

	.page-content {
		padding: 24px 32px;
		margin: 0;
		min-height: calc(100vh - 66px);
		width: calc(100vw - 616px);
		background: $app-white;
		padding-bottom: 72px;

		.btn-container {
			position: fixed;
			bottom: 0;
			left: 64px;
			z-index: 1;
			width: calc(100vw - 616px);
			padding: 16px;
			background: $app-white;
			justify-content: flex-end;
			box-shadow: 0px -4px 15px rgba(0, 0, 0, 0.06);
		}
	}

	.sticky-element {
		display: flex;
	}

	.item-detail-sidebar {
		position: sticky;
		top: 64px;
		right: 0;
		width: 552px;
		height: calc(100vh - 66px);
		background: $app-white;
		border-color: $border-light-color;
		.marketplace-app-container {
			margin: 16px;
			.expandable-app {
				border: 1px solid $border-light-color;
				border-radius: 8px;
				box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.01);
				box-sizing: border-box;
			}
			.ui-accordion-header-trigger {
				height: 18px;
				@include flexbox-simple(space-between, center);
				padding: 20px 16px;

				.accordion-title-wrap {
					@include flexbox-simple(normal, center);
					font-weight: $text-weight-semi-bold;
				}
			}
		}
	}

	.slot-availability-container {
		margin: 40px 0 0 32px;
	}

	.img-container {
		display: flex;
		width: 112px;
		height: 112px;
		background: $background-color-2;
		border-radius: $radius;
		padding: 8px;

		img {
			max-width: 100%;
			max-height: 100%;
			margin: auto;
		}
	}

	.item-primary-info, .desc-container {
		margin-bottom: 24px;
	}

	.item-info {
		display: flex;
		flex: 1;
		flex-direction: column;
		padding-left: 16px;

		> * {
			margin-bottom: 12px;
		}

		.short-desc {
			color: $secondary-text-color-1;
			font-size: $title-small;
		}

		.delivery .ember-svg {
			fill: $app-warning-state;
			margin-bottom: -1px;
		}
	}

	.desc-container {
		font-size: $baseFontSize;
		line-height: 160%;
		margin-bottom: 0;

		.btn-read-more {
			margin-left: -8px;
		}
	}

	.view-more-component {
		margin-bottom: 16px;

		.view-more-toggle {
			font-weight: $text-weight-semi-bold;
		}
	}

	.ember-form .custom-fields {

		margin-bottom: 16px;

		.static-rich-text-field {
			margin-top: 16px;
			img {
				max-width: 100%;
			}
			ul {
				list-style: initial;
			}
		}

		.form-field {
			max-width: 600px;
			margin-top: 16px;
			margin-bottom: 0;

			&.textarea-field {
				max-width: none;
			}

			&.checkbox-field {
				
				&:not([hidden]):not(.chk-request-for) {
					margin-top: 24px;
					display: block;
				}

				label {
					margin: 0;
					display: flex;
					align-items: center;
					input {
						margin-left: 0;
					}
				}

				.form-field--errors {
					margin-top: 8px;
				}
			}

			.ember-power-select-option.custom-select-option {
				&.level-1 {
					padding-left: 24px;
				}
				&.level-2 {
					padding-left: 48px;
				}
			}
		}

		@import 'nested-child';

		.loaner-fields-wrapper {
			display: flex;
			align-items: center;
			@import "slot-availability";
		}

		.ember-power-select-options {
			max-height: 30vh;
			overflow-y: auto;
		}
	}

	section.attachments {
		margin: 16px 0;

		.attach-files {
			padding: 16px 0;
			margin: 0;
			background: none;
		}

		.error {
			color: $app-error-state;
		}
	}

	.grid-container {
		display: grid;
		margin: 36px;
		grid-template-columns: 152px 96px 96px;
		grid-gap: 8px 32px;

		.line-90 {
			max-width: 132px;
		}

		.line-40 {
			max-width: 56px;
		}
	}

	&.ember-modal-dialog.ember-modal-dialog.ember-modal-side .ember-modal-body.add-req-item-detail {
		height: calc(100% - 120px);
	}

	.add-req-item-detail {
		#attachment-list-container {
			z-index: 0;
		}
		.img-container {
			padding: 5px;
			width: 80px;
			min-width: 80px;
			max-height: 80px;
		}
		.slot-availability-container {
			margin: 16px 0 0 0;
		}
	}

	.item-detail-sidebar, #add-item-qty-details {
		table {
			width: calc(100% - 72px);
			text-align: left;
			margin: 24px 36px;
	
			td, th {
				padding: 8px 0;
			}
	
			th {
				font-weight: $text-weight-semi-bold;
				font-size: $baseFontSize;
				color: $primary-text-color-1;
			}
			
			tr {
				min-height: 44px;
			}
	
			.td-price {
				width: 140px;
			}
	
			td.td-price, td.qty-item-name {
				padding: 12px 0 4px 0;
			}
	
			@import "qty-btns";
		}
		.total {
			display: flex;
			justify-content: space-between;
			padding: 16px 0;
			margin: 48px 36px;
			border-top: 1px dashed;
			border-bottom: 1px dashed;
			border-color: $border-dark-color;
			text-transform: uppercase;
			font-size: 16px;
			font-weight: bold;
	
			.total-label {
				padding-left: 0;
			}

			.total-cost {
				min-width: 140px;
			}
		}
	}

	&.ember-modal-dialog .ember-modal-footer {
		#add-item-qty-details {
			#qty-cost-summary {
				display: flex;
				align-items: center;
				font-weight: $text-weight-semi-bold;
				font-size: $text-normal;
				padding-left: 4px;
				.total-cost-info {
					display: flex;
					.total-label::after {
						content: ":";
						padding: 0 5px;
					}
				}
				.view-details {
					display: flex;
					justify-content: center;
					align-items: center;
					&.show-cost {
						&:before {
							content: "";
							border-right: 1px solid $dotLineSeparatorBg;
							height: 30px;
							margin: 0 8px 0 36px;
						}
					}
					.btn-link {
						font-size: $text-normal;
						font-weight: $text-weight-semi-bold;
						display: flex;
						align-items: center;
						svg {
							margin-left: 8px;
						}
					}
				}
			}
			.catalog-item-sidebar-scrollable {
				width: 100%;
				box-shadow: 0px -9px 15px rgba(0, 0, 0, 0.05);
				background: $app-white;
				&.force-expand-details {
					height: 50px;
				}
				&.details-collapsed {
					height: 0;
					transition: height 2s;
				}
				&.details-expanded {
					height: inherit; //todo - need to add animation
					max-height: 350px;
					transition: height 2s;
					table {
						height: 100%;
					}
				}
	
				table {
					font-weight: $text-weight-normal;
					font-size: $baseFontSize;
					margin: 0 32px 0 36px;
					tbody {
						.qty-item-name, .td-price {
							padding: 0;
						}
						.qty-btn-container {
							justify-self: flex-end;
						}
						.qty-btns {
							padding: 0;
						}
						.td-price {
							max-width: 70px;
						}
						.qty-item-name {
							max-width: 150px;
						}
					}
				}
			}
		}
	}

	/* ----- Loader Styles ------ */
	&.loader {
		.item-primary-info, .desc-container {
			margin-bottom: 16px;
		}
	}

	.shimmer-loader-item-fast {
		margin: 0 0 16px 0;
		max-width: 600px;
	}

	.line {
		float: none;

		&.l1 {
			width: 184px;
		}
		&.l2 {
			width: 136px;
		}
		&.l3 {
			width: 224px;
		}
	}

	.inp-field {
		height: 32px;
	}

	.inp-field, .box-reactangle {
		margin-bottom: 24px;
	}

	.title-small {
		font-weight: $text-weight-semi-bold;
		color: $primary-text-color-1;
	}
}


@media (min-width: 1921px) {
	.request-item-detail .main-content-container {
		max-width: 1048px;
		margin: auto;
	}
}
