.list-catalog-items {

	display: grid;
	grid-template-columns: 312px 312px 312px;
	grid-gap: 16px;
	padding-bottom: 24px;

	.catalog-item-card {
		box-sizing: border-box;
		padding: 16px;
		align-items: flex-start;
		height: 100%;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		border-radius: 8px;
		border: 1px solid $border-light-color;

		&:hover {
			border-color: $border-light-blue;
		}

		img.item-icon {
			width: 64px;
			max-height: 64px;
			padding: 4px;
			background: $background-color-2;
			border-radius: $radius;
		}

		.info-container {
			margin-left: 16px;
			width: calc(100% - 80px);
			color: $primary-text-color-1;

			.item-name, .cost {
				font-weight: 600;
				font-size: $title-small;
			}

			.item-desc, .delivery {
				font-weight: 400;
				font-size: $text-normal;
				color: $secondary-text-color-1;
			}

			.item-desc {
				width: 100%;
				margin: 8px 0;
			}

			.item-attributes-info {
				display: flex;
			}

			.cost {
				margin-right: 16px;
			}

			.delivery {
				display: flex;
				align-items: center;

				.ember-svg {
					fill: $primary-text-color-1;
					margin-right: 4px;
				}
			}
		}
	}
}

.catalog-items-empty {
	width: 100%;
	height: 240px;
	margin: 16px auto 80px;
	text-align: center;

	img {
		margin-bottom: 32px;
	}

	h2 {
		margin-bottom: 8px;
		color: $primary-text-color-1;
	}

	p {
		color: $secondary-text-color-1;
	}
}

.loader-container {
	padding: 8px;
	box-sizing: border-box;
}
