.execute-scenario-modal {
	.ember-modal-header {
		padding: 0 !important;

		.execute-scenario-modal-header {
			display: inline-block;
			width: 600px;
			height: 85px;

			h2 {
				display: inline-block;
				font-weight: $text-weight-semi-bold;
				font-size: $title-extra-large;
				padding: 24px 0 32px 40px;
			}

			.execute-scenario-new {
				position: relative;
				top: 24px;
				right: 32px;
				min-width: 80px;
				float: right;
				background: $white;
				@include flexbox-simple(space-around, center);
			}
		}
	}

	.ember-modal-body {
		padding: 0 40px 40px !important;
		height: calc(100% - 96px) !important;
	}

	.execute-scenario-modal-body {
		margin-top: -20px;

		.dropdown-search {
			position: relative;
			width: 16px;
			height: 20px;
			left: 8px;
			top: 32px;
			fill: $disabled-text-color;
		}

		.scenario-search-input {
			width: 520px;
			margin-top: 1px;
			padding-left: 32px;
		}

		.loader-container {
			align-items: center;
			@include flexbox;
			position: relative;
			width: 100px;
			top: 100px;
			left: 230px;

			svg {
				fill: $app-primary;
				height: 28px;
				width: 28px;
			}
		}

		ul {
			margin-top: 26px;

			li {
				border: 1px solid $border-dark-color;
				width: 520px;
				padding: 16px;
				box-sizing: border-box;
				box-shadow: inset 0px 1px 2px rgba(24, 50, 71, 0.05);
				border-radius: 4px;
				margin-bottom: 16px;

				&:focus, &:hover{
					border-color: $inputFocusBorder;
					box-shadow: 0px 2px 8px rgba(22, 29, 37, 0.09);
					transition: border-color 0.5s ease-in-out;
					.execute-btn{
						opacity: 1;
					}
				}

				.scenario-access-label {
					border-radius: 4px;
					padding: 0 7px;
					background: $background-color;
					font-weight: $text-weight-medium;
					font-size: $text-normal;
					height: 20px;
					display: inline-block;
					svg {
						position: relative;
						top: 1px;
					}
				}

				.execute-btn {
					opacity: 0;
					background: $white;
					float: right;
					transition: 0.3s ease;
					@include flexbox;
					@include align-items(center);
					color: $primary-text-color-1;
					&:hover {
						background: $background-color-2;
					}
					&:focus {
						opacity: 1;
					}
					svg {
						margin-right: 4px;
						fill: $primary-text-color-1;
					}
				}

				.scenario-title {
					margin-top: 6px;
					font-weight: $text-weight-semi-bold;
					font-size: $title-small;
				}

				.scenario-desc {
					font-size: $title-small;
					margin: 8px 0 0;
				}
			}
		}
		.empty-search-results {
			margin-top: 56px;
			color: $app-form-label-color;
		}
		.no-scenarios {
			text-align: center;
			margin-top: 200px;
			.scenario-empty-img {
				margin-bottom: 24px;
			}
			.empty-scenario-msg {
				color: $secondary-text-color-1;
				font-size: $title-normal;
				margin-bottom: 20px;
			}
			.execute-scenario-new {
				font-weight: $text-weight-medium;
				svg {
					vertical-align: middle;
				}
				span {
					vertical-align: bottom;
				}
			}
		}
	}
}
