.ember-badge {
	display: inline-block;
	font-size: 12px;
	font-weight: 600;
	line-height: 1;
	text-align: center;
	padding: 4px 8px;
	border-radius: 4px;
	box-sizing: border-box;
	background-color: $app-white;
	border: 1px solid $border-dark-color;

	svg {
		width: 14px;
		height: 10px;
	}

	/* badge variants based on size */

	&-large {
		padding: 8px;
	}
	&-small{
		padding: 2px 4px;
	}

	/* shown in tab component */
	&-count {
		color: $app-form-label-color;
		border: none;
		border-radius: 16px;
	}

	/* badge variants based on color */

	&-light {
		color: $app-grey;
		background-color: $background-color;
		border-color: $border-dark-color;
	}

	/* lighter badges with thinner font and no border */
	&-lighter {
		padding: 6px 8px;
		border: none;
		font-weight: $text-weight-normal;
	}

	&-success, &-optimally {
		color: #007958;
		background-color: #e0f5f0;
		border-color: #b4e5d9;
	}

	&-error, &-over {
		color: #c82124;
		background-color: #ffecf0;
		border-color: #ffd0d6;
	}

	&-warning, &-under {
		color: #c13e1b;
		background-color: #fef1e1;
		border-color: #fddbb5;
	}

	&-blue, &-info {
		color: #2c5cc5;
		background-color: #e5f2fd;
		border-color: #bbdcfe;
	}

	&-blue-inverse {
		color: $app-white;
		background-color: $app-primary;
		border-color: $app-primary;
	}

	&-blue2 {
		color: #678ce7;
		background-color: #edf2ff;
		border-color: #678ce7;
	}

	&-orange {
		color: #c13e1b;
		background-color: #fef1e1;
		border-color: #fdfbb5;
	}

	&-grey {
		border: none;
		color: $app-grey;
		background-color: $app-border-primary;
	}

	&-full-orange {
		border: none;
		color: $app-white;
		background-color: $app-warning-state;
	}
}
