.linked-assets-tab-container {
	.linked-assets-list-container {
		.associate-assets-btn {
			float: right;
		}
	}
	.associate-assets-btn {
		margin: 0 0 16px;
		border-top-width: 0;
		font-family: $baseFontFamily;
		@include flexbox;
		@include align-items(center);
		font-weight: $text-weight-medium;
		padding-right: 0;
		.add-btn-text:hover {
			color: $linkColorHover;
		}
		.add-icon {
			width: 16px;
			height: 16px;
			margin-right: 5px;
		}
	}
	.linked-assets-list {
		clear: both;
		.asset-details {
			margin: 16px 0;
			list-style: none;
			position: relative;
			.asset-icons {
				@include flexbox;
				@include align-items(center);
				.asset-list-icons {
					padding: 0px 5px;
					&.asset-icon {
						@include flexbox;
						@include align-items(center);
						border: 1px solid $border-light-blue;
						border-radius: 4px;
						background: $section-background-blue;
						width: 32px;
						height: 32px;
						margin: 0px 5px;
						.asset {
							fill: $linkColor;
							width: 20px;
							height: 18px;
						}
					}
					&.remove-asset {
						background: $white;
						border: 1px solid transparent;
						padding-top: 5px;
						padding-left: 0;
						&[disabled] {
							opacity: 0.5;
							pointer-events: none;
						}
						.remove-icon {
							width: 16px;
							height: 16px;
						}
					}
				}

			}
			.asset-detail {
				width: 65%;
				margin-left: 10px;
				.asset-name {
					color: $app-black;
					font-weight: $text-weight-medium;
					font-size: $baseFontSize;
				}
				.asset-link {
					@extend .ellipsis;
					max-width: 60%;
					display: inline;
					
					.asset-link-name {
						max-width: 90%;
						font-weight: $text-weight-medium;
					}
				}
				label {
					color: $app-form-label-color;
					font-size: $baseFontSize;
					display: inline-block;
					padding-bottom: 0;
				}
				.asset-department,
				.asset-location {
					label,
					.asset-field-value {
						font-size: $text-normal;
					}

				}
				.dep-loc-separator {
					border: 3px solid $border-dark-color;
					display: inline-block;
					border-radius: 50%;
					vertical-align: middle;
					margin: 0 5px 0;
				}

				@media only screen and (max-width: 1325px) {
					.asset-department,
					.asset-location {
						display: block;
					}
					.dep-loc-separator {
						display: none;
					}
				}
				.open-new-tab {
					padding-left: 8px;
					margin-top: 0;
					vertical-align: middle;
					svg {
						width: 12px;
						height: 12px;
						fill: $linkColor;
					}
				}
			}
			.fullfil-batch {
				position: absolute;
				right: 29%;
				background: $white;
				border: 1px solid $border-dark-color;
				box-sizing: border-box;
				border-radius: 4px;
				padding: 3px 7px 5px;
				.fulfilled-icon {
					width: 14px;
					height: 14px;
					vertical-align: middle;
				}
				.fullfill-text {
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;
					color: $primary-text-color-1;
					vertical-align: middle;
				}
			}
			.asset-properties {
				width: 25%;
				text-align: left;
				border-left: 1px solid $border-light-color;
				margin-left: 150px;
				.asset-user,
				.asset-impact,
				.software-type,
				.asset-type,
				.asset-quantity
				{
					margin-left: 15px;
					@include flexbox;
					@include align-items(center);
					label {
						@extend .ellipsis;
					}
				}
				.asset-impact-value {
					@include flexbox;
					@include align-items(center);
				}
				.priority-indicator {
					width: 10px;
					height: 10px;
					background: $priorityLowColor;
					border-radius: 2px;
					display: inline-block;
					margin: 0 5px 0 1px;
					&.low {
						background: $priorityLowColor;
					}
					&.medium {
						background: $priorityMediumColor;
					}
					&.high {
						background: $priorityHighColor;
					}
				}
				label {
					color: $app-form-label-color;
					font-size: $baseFontSize;
					display: inline-block;
					padding-bottom: 3px;
					width: 40%;
				}
				.asset-field-value {
					width: 60%;
				}
			}
			.asset-field-value {
				color: $primary-text-color-1;
				font-size: $baseFontSize;
				font-weight: $text-weight-medium;
				&.ellipsis {
					max-width: 100px;
					display: inline-block;
					vertical-align: middle;
				}
			}
			.edit-asset {
				border-radius: 4px;
				background: $white;
				border: 1px solid $border-dark-color;
				.edit-icon {
					fill: $app-icon-color;
					width: 12px;
					height: 12px;
				}
				&.hidden {
					visibility: hidden;
				}
			}
			@media only screen and (max-width: 1150px) {
				.fullfil-batch {
					right: 31%;
				}
			}
		}
	}
}

.edit-asset-modal, .fulfill-asset-modal {
	.ember-modal-header {
		.title {
			font-weight: $text-weight-semi-bold;
		}
		.desc {
			font-size: $title-small;
			color: $secondary-text-color-1;
			font-weight: $text-weight-normal;
			margin-top: 10px;
		}
	}
	.ember-modal-body {
		.asset-details-header {
			font-weight: $text-weight-semi-bold;
			font-size: $title-small;
			color: $primary-text-color-1;
		}
		.asset-form-fields {
			.form-field {
				margin: 15px 0;
				label {
					margin-bottom: 8px;
					color: $secondary-text-color-1;
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;
				}
			}

			.ember-power-select-option[aria-selected=true] {
				&:before {
					right: 5px;
				}
				&.user-select-option::before{
					top: 15px;
				}
			}


			.asset-stage-container {
				border-top: 1px solid $border-dark-color;
				margin-top: 20px;
				.asset-stage-heading {
					font-weight: $text-weight-semi-bold;
					font-size: $title-small;
					color: $primary-text-color-1;
					margin: 15px 0;
				}
			}

			.has-department {
				margin-left: 20px;
				border-left: 1px solid $border-light-color;
				padding-left: 10px;
			}

		}
	}
}

.remove-asset-confirm-modal  {
	.ember-modal-body {
		padding-bottom: 32px !important;
	}
}

.linked-assets-tab-container,
.associate-asset-modal {
	.empty-asset-list {
		text-align: center;
		margin-top: 150px;
		.empty-state-content {
			margin-top: 15px;
			color: $secondary-text-color-1;
			font-size: $title-normal;
			.empty-state-hint {
				margin-top: 10px;
			}
		}
		.associate-assets-btn {
			margin-top: 20px;
		}
	}
}
