@import './watchers';

.ticket-show-page {
	.tkt-quick-action {
		display: inherit;
		.btn, .association-dropdown  {
			background: $white;
		}

		.btn:hover {
			background: $background-color-2;
		}

		.bulk-more-wrapper {
			margin-right: 8px;
			.bulk-more-trigger {
				width: 32px;
				height: 32px;
				border-radius: 4px;
			}
			.bulk-action-btn {
				&:focus-visible {
					border: 2px solid $linkColor;
					box-shadow: none;
					outline: none;
				}
			}
			.bulk-more-content {
				min-width: 198px;
				max-height: 415px;
				overflow: scroll;
				.btn-list-item {
					margin-bottom: 4px;
				}
			}
		}

		.quick-action-btn, .edit-tickets-btn {
			padding: 0 15px;
		}

		.aws-notification-toggle {
			width: 32px;
			svg{
				margin-top: 2px;
				margin-left: -4px;
			}
		}

		.mkp-app-quick-nav-bar {
			float: left;
		}

		.navigate-items {
			display: flex;
			align-items: center;
			> .item-nav-btn {
				padding: 5px;
				background: $white;
				&.prev-item {
					border-top-left-radius: 4px;
					border-bottom-left-radius: 4px
				}
				&.next-item {
					border-top-right-radius: 4px;
					border-bottom-right-radius: 4px;
					margin-right: 0;
				}
				&:hover {
					background: $background-color-2;
				}
				&:focus-visible {
					border: 2px solid $linkColor;
					box-shadow: none;
					outline: none;
				}
				&.disabled {
					cursor: default;
					box-shadow: none;
					@include svg-fill($btnDisabledColor !important);
				}
			}
			.nav-tooltip {
				width: auto;
				text-align: center;
				// this is to center align tooltips
				&.prev { min-width: 109px; }
				&.next { min-width: 86px; }
			}
		}

		.quick-action-tooltip {
			width: max-content;
			margin: 8px 0 0;
		}

		.unwatch-icon-tooltip {
			margin: 0px !important;
		}

		.conversation-actions-dropdown {
			margin-right: 8px;
			.conv-options-trigger {
				height: 32px;
				.convo-action-btn {
					padding: 0 8px;
					margin-right: 0;
					.default-action {
						padding: 0 8px;
					}
				}
			}
			.convo-actions-content {
				min-width: 130px;
				margin-right: 8px;
				.basic-dropdown-item {
					font-weight: $text-weight-medium;
				}
			}
		}
	}
	.quick-actions-bar {
		.ticket-state-wrapper {
			height: 22px;
			margin: 5px 12px 0 0;
			@extend .ticket-state-badge;
		}
	}
}
