.ticket-show-page {
	.tkt-quick-action {
		.ticket-watcher-wrapper {
			margin-right: 8px;
			.ticket-watcher-trigger {
				width: 32px;
				height: 32px;
				border-radius: 4px;
			}
			.watching {
				border: 1px solid $app-warning-state !important;
			}
			.watcher-content {
				min-width: 250px;
				.ticket-watchers-container {
					padding: 4px 6px;
					font-size: $text-normal;
					.watcher-header {
						@include flexbox-simple(normal, center);
						.watcher-count {
							font-weight: $text-weight-semi-bold;
						}
						.btn-link {
							font-size: $text-normal;
							color: $inputFocusBorder;
							font-weight: $text-weight-medium;
						}
					}
					.watcher-description {
						font-weight: normal;
						color: $app-form-label-color;
						line-height: 17px;
					}
					.search-agents {
						padding-top: 8px;
						.watcher-icon {
							z-index: 1;
							position: absolute;
							padding: 10px 0 0 9px;
						}
						.ember-basic-dropdown-content {
							.ember-power-select-options {
								max-height: 210px;
							}
						}
						.ember-power-select-trigger-multiple-input {
							padding-left: 29px;
							font-size: $baseFontSize;
							color: $secondary-text-color-1;
						}
						.ember-power-select-option {
							font-size: $baseFontSize;
							color: $primary-text-color-1;
							font-weight: $text-weight-normal;
						}
					}
					.watchers-list {
						padding: 5px 0;
						.ticket-watcher {
							@include flexbox-simple(flex-start, center);
							margin-top: 2px;
							margin-left: 1px;
							background: $white;
							.avatar__initial {
								width: 24px;
								height: 24px;
								cursor: default;
								font-size: 16px;
							}
							.remove-watcher-icon {
								border: 2px solid transparent;
								background: $white;
								padding-top: 7px;
								box-shadow: none;
								outline: none;
								.remove-icon {
									width: 16px;
									height: 16px;
								}
								&:focus-visible {
									box-shadow: 0 0 0 2px $inputFocusBorder;
								}
							}
							.watcher-avatar {
								padding-left: 4px;
							}
							.watcher-name {
								padding-left: 7px;
								max-width: 160px;
								word-wrap: break-word;
							}
						}
						.new-watcher-animation {
							animation: add_watcher_bg ease-in-out 0.8s;
							@keyframes add_watcher_bg {
								0% {
									background: $white;
								}
								50% {
									background: $app-casablanca-light;
								}
								100% {
									background: $white;
								}
							}
						}
					}
					.shimmer-loader-item {
						margin-top: 16px;
						margin-left: 5%;
					}
					.empty-sidemodal-list-content {
						margin: 10px 0 0 0;
					}
					.empty-states {
						padding-top: 5px;
						.empty-state-messages {
							padding: 2px 0;
							margin: 0;
							font-size: $text-normal;
							color: $app-form-label-color;
						}
					}
				}
			}
		}
	}
}
