.request-item-detail {

	.additional-items-title {
		margin-bottom: 16px;
		margin-top: 34px;

		h2 {
			margin-bottom: 8px;
		}

		p {
			font-size: 12px;
			color: $secondary-text-color-1;
		}
	}

	.selected-items-title {
		margin-top: 24px;
		margin-bottom: 16px;
	}

	.search-background {
		position: relative;
		height: 48px;
		padding: 8px 0;
		width: 450px;

		* {
			position: absolute;
		}

		#search-child-items {
			width: 450px;
			padding-left: 32px;
			padding-right: 100px;
		}

		.ember-svg {
			top: 14px;
			left: 8px;
		}

		#clear-items-search {
			top: 50%;
			right: 4px;
			transform: translateY(-50%);
			color: $linkColor;
			font-weight: initial;
		}

		.btn-link:focus {
			border: 2px solid $linkColor;
			border-radius: 4px;
		}

	}

	.no-items {
		text-align: center;
		padding: 16px 0 84px 0;

		div {
			margin-top: 16px;
		}
	}
	
	#btn-show-selected {
		margin: -12px auto 32px 50%;
		transform: translateX(-50%);
	}

	.accordion-list.selected {
		margin-bottom: 32px;
	}

	.accordion-list .accordion {
		&-item {
			border: 1px solid $border-light-color;
			margin: 8px 0;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			border-radius: 8px;

			input.chk-child-item {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				margin-left: 30px;

				/* TODO: Have to clean up as common fix
				   Adding !important as the default disbled checkbox is already overridden in
				   https: //github.com/freshdesk/itildesk/blob/prestaging/public/src/pattern/lib/_forms.scss#L249
				*/
				&[disabled] {
					background: $checkboxDisabledBackground !important;
					border: 1px solid $checkboxDisabledBorder !important;
				}
			}

			.accordion-arrow {
				position: absolute;
				right: 16px;
				top: 50%;
				transform: translateY(-50%);
			}

			&.is-expanded .accordion-arrow {
				transform: rotate(90deg);
				margin-top: -7px;
			}
		}

		&-header {
			position: relative;
			width: 100%;

			&.shimmer-loader-item-fast {
				height: 80px;
				width: 100%;
				max-width: none;
				margin: 0;
				opacity: 0.75;
			}
		}

		&-trigger {
			display: flex;
			align-items: center;
			border: none;
			padding: 12px;
			min-height: 80px;
			width: 100%;
			padding-left: 64px;
			cursor: pointer;
			border-radius: 8px;

			&, &:hover {
				background: $app-white;
			}

			img {
				width: 48px;
				height: 48px;
			}

			span {
				padding-left: 36px;
				font-size: 14px;
				color: $primary-text-color-1;
				font-weight: 600;
				text-align: left;

				div {
					padding: 8px 0;
				}
			}
		}

		&-panel {
			padding: 12px;
			padding-left: 148px;
		}
	}
	.error-msg {
		margin: 8px 0;
		color: $app-error-state;
	}
}

#btn-child-items-more {
	margin: 24px auto;
	padding: 8px 16px;
	font-weight: $text-weight-semi-bold;
	display: flex;
	align-items: center;
	border: 1px solid transparent;
	box-shadow: 0 0 0 1px transparent;
	border-radius: 4px;

	&:focus-visible {
		border-color: $inputFocusBorder;
		box-shadow: 0 0 0 1px $inputFocusBorder;
	}
}

.catalog-item-sidebar-scrollable {
	height: calc(100vh - 100px);
	overflow-y: auto;
}