.quick-actions-bar {
	.ticket-agent-collision {
		display: flex;
		align-items: center;
		.ticket-viewers {
			padding-right: 8px;
		}
		.ticket-viewers, .ticket-repliers {
			.ticket-viewers-trigger, .ticket-repliers-trigger {
				height: 24px;
				border-radius: 50%;
				&:focus-visible { box-shadow: none; }
			}
			.viewers-flip, .repliers-flip {
				width: 24px;
				height: 24px;
				cursor: pointer;
				color: $app-black;
				border-radius: 50%;
				align-items: center;
				display: inline-block;
				perspective: 1000px;

				.flip-front, .flip-back {
					width: 24px;
					height: 24px;
					top: 0;
					left: 0;
					display: flex;
					font-size: $title-small;
					line-height: 17px;
					background: $white;
					position: absolute;
					border-radius: 50%;
					align-items: center;
					justify-content: center;
					border: 1px solid $svgFocusedBorder;
					font-weight: $text-weight-semi-bold;
					transform-style: preserve-3d;
					backface-visibility: hidden;
					transition-property: transform;
					transition-duration: 0.75s;

					svg {
						fill: $secondary-text-color-1;
						path { fill: $secondary-text-color-1; }
					}
				}
				.flip-front {
					z-index: 5;
					border-radius: 50%;
				}
				.flip-back {
					z-index: 0;
					border-radius: 50%;
					transform: rotateY(-180deg);
				}
				.ff-active {
					background: $app-primary;
					border: 1px solid $app-primary;
					svg {
						fill: $white; 
						path { fill: $white; }
					}
					animation: rotateFrontCircle 4s 1s infinite;
				}
				.fb-active {
					color: $white;
					background: $app-error-state;
					border: 1px solid $app-error-state;
					animation: rotateBackCircle 4s 1s infinite;
				}
				&:not(.disable-flip):hover {
					.flip-front {
						z-index: 5;
						transform: rotateY(180deg);
					}
					.flip-back  {
						z-index: 10;
						transform: rotateY(0deg);
					}
				}
			}
			.ticket-viewers-content, .ticket-repliers-content {
				right: auto;
				z-index: 15;
				margin-top: 8px;
				overflow: hidden auto;
				min-width: 185px;
				.empty-collision {
					line-height: 14px;
					font-size: $text-normal;
					color: $secondary-text-color-1;
				}
				.agents {
					min-width: 212px;
					max-height: 232px;
					padding: 3px 8px;
					.collision-header {
						padding-bottom: 6px;
						text-transform: uppercase;
						line-height: 14px;
						font-size: $text-normal;
						color: $secondary-text-color-1;
					}
					.ticket-viewers, .ticket-repliers {
						padding: 4px 0;
						display: flex;
						align-items: flex-start;
						.viewers-name, .repliers-name {
							min-width: 152px;
							max-width: 212px;
							font-size: $text-normal;
						}
						.viewers-name {
							padding: 3px 8px 0;
						}
						.repliers-name {
							padding: 3px 16px 0 8px;
						}
						.avatar__initial {
							width: 24px;
							height: 24px;
							cursor: default;
							font-size: $title-normal;
						}
					}
					.ticket-repliers {
						.typing-wave {
							margin-top: -1px;
						}
					}
				}
			}
		}
	}
}

// Agent collision Animation
@keyframes rotateFrontCircle {
	0%, 16%, 83%, 100% { transform: rotateY(0deg); }
	33%, 67% { transform: rotateY(180deg); }
}
@keyframes rotateBackCircle {
	0%, 16%, 83%, 100% { transform: rotateY(180deg); }
	33%, 67% { transform: rotateY(0deg); }
}
