.associated-alerts-list-wrapper {
	.tkt-alert-card-wrapper {
		margin-bottom: 8px;
		border: 1px solid $border-light-color;
		border-radius: 8px;
		padding: 16px;
		@include flexbox-simple(center, center);
		position: relative;
		max-height: 70px;
		&:nth-child(1) {
			margin-top: 4px;
		}
		&:hover {
			border-color: $border-light-blue;
		}
		.tkt-alert-card-icons {
			@include flexbox;
			align-items: center;
			.tkt-remove-alert-btn {
				border: 1px solid transparent;
				background: none;
				padding: 3px 5px 5px 0;
				svg {
					vertical-align: middle
				}
				&[disabled] {
					opacity: 0.5;
					pointer-events: none;
				}
			}
			.tkt-alert-icon {
				border: 1px solid $border-light-blue;
				border-radius: $radius;
				background: $section-background-blue;
				height: 32px;
				width: 32px;
				@include flexbox-simple(center, center);
				margin: 0 5px;
				svg {
					width: 16px;
					height: 16px;
				}
			}
		}
		.tkt-alert-details {
			flex: 6;
			width: 75%;
			margin-left: 10px;
			min-width: 0;
			.tkt-alert-link-title {
				font-weight: $text-weight-medium;
				@include flexbox;
			}
			.tkt-alert-link {
				min-width: 0;
				max-width: 80%;
				margin-left: 5px;
			}
			.tkt-alert-open-new-tab {
				vertical-align: middle;
				svg{
					width: 12px;
					height: 12px;
					fill: $linkColor;
				}
			}		
		}
		.tkt-alert-date-profile {
			@include flexbox;
			padding-top: 3px;
			color: $app-form-label-color;
			.tkt-alert-source {
				margin-right: 5px;
				max-width: 17%;
				.freddy-icon {
					width: 18px;
					height: 18px;
				}
			}
			.tkt-alert-updated-at {
				max-width: 40%;
			}
		}
		.tkt-alert-properties {
			width: 25%;
			text-align: left;
			min-width: 0;
			border-left: 1px solid $border-light-color;
			.tkt-alert-status,
			.tkt-alert-severity {
				margin-left: 16px;
				@include flexbox;
				@include align-items(center);
			}
			.tkt-alert-status {
				padding-top: 3px;
			}
			.tkt-alert-status-label,
			.tkt-alert-severity-label {
				color: $app-form-label-color;
				flex: 2;
				padding-bottom: 0;
			}
			//TODO: This css for severity-indicatior need to reused from ams/common.scss
			.tkt-alert-severity-indicatior {
				width: 10px;
				height: 10px;
				border-radius: 2px;
				margin-right: 5px;
				display: inline-block;
				&.critical {
					background: $critical-state;
				}
				&.error {
					background: $error-state;
				}
				&.warning {
					background: $warning-state;
				}
				&.ok {
					background: $ok-state;
				}
			}
			.tkt-alert-status-data,
			.tkt-alert-severity-data {
				flex: 3;
				font-weight: $text-weight-medium;
			}

			.tkt-alert-severity-indicatior.ok + .tkt-alert-severity-value {
				text-transform: uppercase;
			}

		}
	}
}

.detach-ticket-alert-confirm-modal {
	//using default ember-modal-dialog width since confirm message is huge 
	width: 512px !important; 
}