$ticket-details-sidebar-width: 415px;

.associated-tickets .separator {
	margin-left: auto;
}

.ticket-show-page {
	&.is-sticky {
		.page-layout-content {
			.page-sidebar,
			.page-content-with-sidebar {
				margin-top: 96px !important;
			}
		}
	}
	.page-title {
		align-items: normal !important;
		flex-direction: column;
		margin: 0;
		.ticket-tabs {
			margin-left: 0px !important;
			border-bottom: 1px solid $app-grey-bg-2;
			background-color: $white;
			height: 42px;
			z-index: 9;
			.multitab-navbar { height: 40px !important; }
		}
	}
	.page-layout-content .page-content {
		min-height: calc(100vh - 55px) !important;
		margin: 0px !important;
		border-radius: 0;
		.pane-content {
			height: auto;
			padding-right: 16px;
			padding-left: 16px;

			.tab-pane-content {
				// Generic styles for each card in every tab content
				.content-row-item-card {
					@include flexbox-simple(normal, center);
					height: 70px;
					width: auto;
					padding: 12px 16px;
					background: $white;
					box-sizing: border-box;
					@include border-style($border-light-color, 8px);
					box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
					margin-top: 12px;

					&:first-of-type {
						margin-top: 16px;
					}

					&:hover {
						@include transition(0.1s linear);
						@include border-style($border-light-blue, 8px);
						box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
					}
				}
			}
		}
		border-radius: 0px;
	}
	&.page-layout__page-wrapper {
		.sticky-element-container {
			height: auto !important;
		}
		.sticky-element {
			padding: 0;
			background: $white;
			max-height: 350px;
			margin: 0;

			&.sticky-element--sticky {
				box-shadow: none;
				z-index: 100;
				.ticket-summary-and-actions {
					margin-top: 0 !important;
					.quick-actions-bar {
						padding: 11px 32px 12px;
						border-bottom: 1px solid $modal-footer-border-color;
					}
				}
				.tickets-subject-container,
				.ticket-details-cc-list,
				.ticket-details-to-list { // animate to hide when sticky
					// display: none;
					transition: height 0.1s linear, opacity 0.1s linear;
					height: 0;
					opacity: 0;
					z-index: -1;
				}
				.multitab-navlist {
					padding-left: 17px;
				}
			}
		}
	}

	.page-content-with-sidebar {
		width: 100% !important;
		background: $white;
		margin: 0;
		padding: 24px 16px 0;
		.ticket-details-page-content {
			width: calc(100% - #{$ticket-details-sidebar-width}) !important;
		}
	}

	.sticky-element {
		margin: 0 5px;
	}

	.page-layout-content {
		display: flex;
	}

	.page-sidebar {
		width: 0;
		transition: width 0.4s ease-in;
		z-index: auto;
		display: flex;

		.ticket-details-sidebar {
			right: 0;
			top: 64px;
			height: 100%;
			position: fixed;
			display: inline-block;
			background: $background-color-2;
			width: $ticket-details-sidebar-width;
			border-left: 1px solid $border-light-color;

			.suggestion-link-header {
				width: 366px;
				margin: -16px 0 16px -16px;
				background: $app-casablanca-light !important;
				height: 36px;
				border-bottom: 1px solid $alertPartialBorder;
				@include flex-center;

				.btn-link {
					font-weight: $text-weight-medium;
					color: $linkColor;
					@include flex-center;

					.svg-16 {
						height: 20px;
						margin-right: 8px;
					}
				}
			}
		}
	}

	.hide {
		display: none;
	}

	.pull-left {
		float: left;
	}

	.pull-right {
		float: right;
	}

	.svg-14 {
		width: 14px;
		height: 14px;
	}

	.ticket-summary-and-actions {
		display: flex;
		flex-direction: column;
		.quick-actions-bar {
			display: flex;
			padding: 11px 32px 14px;
			background-color: $white;
			z-index: 99;
			.right-bar {
				display: flex;
				.tkt-quick-action { display: inherit; }
			}
		}
	}

	.notification-wrapper {
		@include flexbox();
		text-align: center;
		.notification-with-close-wrapper {
			// ticket-details-sidebar width is 415px, thus keeping left as half of it - nav sidebar.
			left: calc(50% - 176px);
			margin-top: -12px;
		}
		.notification-section {
			padding-right: 6px;
		}
	}

	.tickets-subject-container {
		padding: 0px 32px;
		background-color: $white;
		z-index: 10;
	}

	.ticket-conversation-load-more--wrapper {
		display: flex;
		margin-bottom: 0;
		.ticket-conversation-load-more {
			z-index: 1;
		}
		.ticket-conversation-load-more--parallel-lines {
			z-index: 0;
		}
	}

	.svg-16 {
		height: 16px;
		width: 16px;
	}

	.ticket-conversation-card--desc {
		width: 75%;
	}

	.ticket-conversation-card {
		flex-direction: column;
		position: relative;
		.convo-actions {
			position: absolute;
			right: 16px;
			@include flexbox();
		}
		.convo-card-action-btn {
			@include flexbox-simple(center, center);
			background: $white;
			border: 1px solid $border-dark-color;
			border-radius: 4px;
			padding: 4px 6px;
			height: 32px;
			width: 32px;
			margin-left: 4px;
			&:hover {
				background: $background-color-2;
			}
			.convo-card-action-icon {
				fill: $app-icon-color;
			}
		}
		.convo-actions-more-option-menu {
			display: inline-block;
			vertical-align: bottom;
			.ember-basic-dropdown-trigger {
				&:hover,
				&:focus,
				&[aria-expanded] {
					background: $background-color-2;
				}
			}
			.convo-actions-more-btn {
				padding: 7px 8px;
				margin-right: 0;
			}
			.convo-actions-dropdown-menu {
				.convo-actions-option-item {
					text-align: left;
					margin-top: 0;
				}
			}
		}
	}
}

// Responsive case - larger screens
@media (min-width: 1921px) {
	.ticket-show-page .pane-content {
		width: 70%;
		max-width: 1890px;
		margin: 0 auto;
	}
}

.transform-delete {
	animation: slideOut 500ms ease 0s 1;
	animation-fill-mode: forwards;
}

// FadeOut Animation
@keyframes slideOut {
	0% {
		opacity: 1;
		transform: translateX(0);
	}
	100% {
		opacity: 0;
		display: none; // Added intentionally as the element is not getting removed form DOM occupying visible blank white space
		transform: translateX(100px);
	}
}
@-webkit-keyframes slideOut {
	0% {
		opacity: 1;
		-webkit-transform: translateX(0);
	}
	100% {
		opacity: 0;
		display: none;
		-webkit-transform: translateX(100px);
	}
}

@keyframes drop-fade-below {
	0% {
		opacity: 0;
		transform: translateY(20px);
	}

	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

.ember-basic-dropdown-content {
	&.ember-basic-dropdown-content--above.ember-basic-dropdown--transitioning-in {
		animation: drop-fade-below 0.15s;
	}
	&.ember-basic-dropdown-content--above.ember-basic-dropdown--transitioning-out {
		animation: drop-fade-below 0.15s reverse;
	}
}

.tkt-details-insert-cr-modal {
	.ember-modal-footer {
		display: none !important;
	}
}

.split-ticket-confirm-modal {
	width: 480px;
}

.ticket-shimmer-loader-container {
	background: $white;
}
