.ticket-summary-modal {
	overscroll-behavior-y: contain;

	.collab-highlight-btn, .collab-view-discussion-btn {
		display: none;
	}

	.similar-incidents-header {
		@include flexbox-simple(space-between, center);
		margin-bottom: 12px;
		h2 {
			font-weight: $text-weight-semi-bold;
		}
		.page-nav-btns button {
			background: $white;
		}
	}

	.ticket-summary-modal-top-links {
		justify-content: space-between;
		font-weight: $text-weight-semi-bold;
	}

	.ticket-summary-modal-subject {
		font-weight: $text-weight-semi-bold;
		font-size: $text-large;
		line-height: 24px;
	}

	.ticket-summary-modal-sub-title {
		margin: 24px 0 16px;
		font-weight: $text-weight-semi-bold;
		font-size: $title-normal;
		display: flex;
		align-items: center;
		.requested-items-count{
			@extend .flex-center;
			font-size: $text-normal;
			color: $white;
			background: $app-primary;
			box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
			border-radius: 20px;
			height: 18px;
			margin-left: 7px;
			width: 26px;
		}
	}

	.ticket-summary-modal-top-actions {
		margin: 12px 0 16px 0;

		.summary-quick-action-label-shimmer{
			width: 72px;
			.shimmer-loader-item{
				margin: 5px;
			}
		}

		.group-agent-result, .status-result, .priority-result{
			font-weight: $text-weight-medium;
		}

		&.disabled{
			.status-result, .priority-result{
				max-width: 76px;
				padding: 0 5px;
			}
			.assigned-to .agent-group-wrapper.ticket-summary-assign-to{
				width: 170px;
				.group-agent-result{
					display: flex;
					width: 100%;
					.group-ellipsis, .agent-ellipsis{
						max-width: 50%;
					}
				}
			}
		}
		.ticket-summary-assign-to.agent-group-wrapper {
			.agent-group-trigger{
				height: 24px !important;
				margin-top: 0 !important;
				width: auto;
				display: flex;
				padding: 0;
				> span{
					width: auto;
				}
				&:hover, &[aria-expanded]{
					box-shadow: none;
					background: $dropdown-hover;
				}
				.dropdown-chevron{
					fill: $secondary-text-color-1;
				}
			}
		}
		.assigned-to {
			.ticket-summary-modal-top-actions--label{
				max-width: 82px;
			}
			.ticket-summary-assign-to {
				display: flex;
				align-items: center;
				.group-agent-result{
					color: $inputColor;
					padding: 1px 0 0 5px;
					.group-ellipsis{
						max-width: 52px;
					}
					.agent-ellipsis{
						max-width: 55px;
					}
				}
				&::before{
					content: ":";
					margin-right: 2px;
				}
				.shimmer-loader{
					width: 160px;
				}
			}
		}

		.ticket-summary-quick-action--circle::after{
			content: "";
			background: $dotLineSeparatorBg;
			border-radius: 50px;
			width: 4px;
			height: 4px;
			margin: 0 10px 0 5px;
		}

		.ticket-status, .ticket-priority{
			.status-list-wrapper, .priority-list-wrapper{
				display: flex;
				align-items: center;
				&::before{
					content: ":";
					margin-right: 2px;
				}
				.status-result, .priority-result{
					color: $inputColor;
				}
				.priority-list-trigger, .status-list-trigger{
					height: 24px !important;
					padding: 0 5px;
					span{
						padding-top: 2px;
						max-width: 66px;
					}
					.dropdown-chevron{
						fill: $secondary-text-color-1;
					}
					&:hover,  &[aria-expanded] {
						box-shadow: none;
						background: $dropdown-hover;
					}
				}
				.priority-list-trigger span{
					max-width: 70px;
				}
				.priority-color{
					margin: 6px 6px 0 0;
				}
				.shimmer-loader{
					width: 72px;
					.shimmer-loader-item{
						margin: 0 5px;
					}
				}
			}
		}
		.ticket-summary-modal-top-actions--label {
			color: $secondary-text-color-1;
			padding-bottom: 0;
			max-width: 50px;
		}
	}

	.ticket-summary-modal-top-action--dropdown-wrapper.ember-basic-dropdown {
		.ember-power-select-trigger {
			border: none !important;
			box-shadow: none;
			min-height: 8px;
			line-height: inherit;

			&:hover {
				border: none !important;
				box-shadow: none;
			}

			&:focus-visible {
				outline: 1px solid $dropdown-content-border;
			}
		}
	}

	.ticket-detail-description {
		.ticket-conversation-card--desc{
			width: 90%;
			.ticket-conversation-card--desc-name .requester-vip-badge {
				display: inline-flex;
				margin-left: 5px;
			}
			.ticket-cc-mails{
				display: block;
				max-width: 450px;
			}
			.ticket-conversation-card--desc-reported-time{
				margin-top: 3px;
				@include line-clamp($lines: 3);
				.requested-for{
					display: block;
					line-height: 21px;
				}
				.ticket-desc-reported-time {
					display: flex;
					.shimmer-loader {
						width: 120px;
						.shimmer-loader-item {
							margin: 3px 5px 0 5px;
						}
					}
					.ticket-source {
						margin-left: 3px;
					}
					.ticket-reported-time {
						white-space: nowrap;
					}
				}
			}
		}
		.ticket-conversation-card--avatar {
			margin-right: 18px;
		}
		.ticket-conversation-card--desc-text  {
			margin-top: 8px;
			overflow-x: auto;
			width: 450px;
		}
		.phone-call-wrapper{
			margin-top: 8px;
			.call-duration{
				margin-top: 16px;
			}
		}
	}

	.ticket-summary-modal--requested-item {
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		border-radius: 8px;
		padding: 18px 16px;
		margin-top: 12px;
	}

	.ticket-summary-modal--requested-item-image-section {
		width: 40px;
	}

	.ticket-summary-modal--requested-item-image-wrapper {
		width: 40px;
		height: 40px;
		background: $section-background-blue;
		border-radius: $inputBorderRadius;
		@extend .flex-center;
		padding: 5px;
	}

	.ticket-summary-modal--requested-item-data {
		flex: 1;
		flex-direction: column;
		margin-left: 16px;
	}

	.ticket-summary-modal--requested-item-data-title {
		font-weight: $text-weight-semi-bold;
	}

	.ticket-summary-modal--requested-item-data-props {
		font-size: $text-normal;
		margin-top: 4px;

		> div:not(:last-child)::after{
			content: "";
			background: $dotLineSeparatorBg;
			border-radius: 50px;
			width: 4px;
			height: 4px;
			margin: 0 6px 2px 6px;
			display: inline-block;
		}

		&-title{
			color: $secondary-text-color-1;
		}

		&-value {
			font-weight: $text-weight-medium;
		}
	}

	&.ember-modal-dialog {
		.ember-modal-body{
			height: calc(100% - 35px);
		}
		.ember-modal-footer.ticket-has-conversations{
			border-top: 1px solid $border-light-color;
		}
	}

	.ticket-conversation-cards--wrapper .ticket-conversation-card:last-child{
		margin-bottom: 150px;
	}

	.ticket-summary-modal-body-footer{
		margin-bottom: 35px;
	}

	.ticket-conversation-card--attachment-title{
		margin-bottom: 0;
		.ember-tooltip{
			max-width: 210px;
		}
	}

	.attachment-list{
		display: flex;
		flex-wrap: wrap;
		.list-file-item{
			height: 56px;
			padding: 12px;
			display: flex;
			width: 217px;
			margin: 8px 8px 0 0;
			div:first-child{
				align-self: flex-start;
			}
			div svg.file-icon{
				width: 24px;
				height: 24px;
				margin-top: 0;
			}
			.delete-file{
				margin: 0;
				padding: 0 0 0 8px;
			}
			.file-info{
				margin: 0 0 0 10px;
				width: 66%;
				.file{
					max-width: 100%;
					top: 5px;
					cursor: pointer;
					.file-name{
						font-weight: $text-weight-medium;
					}
				}
			}
		}
		.max-size-file-list{
			margin: 0;
		}
	}

	&.ticket-freddy-si-summary-modal {
		.ember-modal-header {
			padding-bottom: 12px !important;
		}
		.ticket-conversation-card { 
			&:not(.ticket-detail-description) {
				flex-direction: column;
				position: relative;
				.ticket-conversation-card--desc{
					width: 80%;
				}
				.convo-actions {
					position: absolute;
					right: 16px;
				}
			}
		}
		.insert-si-content {
			background: $white;
			padding: 4px 6px;
			height: 32px;
			width: 32px;
			margin-right: 0;
			@extend .flex-center;
			&:hover {
				background: $background-color-2 !important;
			}
			svg path{
				fill: $secondary-text-color-1;
			}
		}
	}
}

.phone-call-data{
	p{
		font-weight: $text-weight-semi-bold;
		margin-bottom: 5px;
	}
	span{
		display: inline-block;
	}
}

@keyframes pop-on-top {
	0% {
		transform: matrix(.97,0,0,1,0,12);
		opacity: 0
	}

	20% {
		transform: matrix(.99,0,0,1,0,2);
		opacity: .7
	}

	40% {
		transform: matrix(1,0,0,1,0,-1);
		opacity: 1
	}

	70% {
		transform: matrix(1,0,0,1,0,0);
		opacity: 1
	}

	100% {
		transform: matrix(1,0,0,1,0,0);
		opacity: 1
	}
}
