.tickets-edit-form-container {
	.ember-modal-footer {
		.modal-update-btn:disabled {
			border: 1px solid $disabled-button-border;
			background: $disabled-button-bg;
			color: $white !important;
			border-radius: 4px;
			opacity: 1;
		}
	}
	.requester-field {
		position: relative;
		.add-new-requester-btn {
			position: absolute;
			top: 0;
			right: 0;
			@include inline-flexbox;
			@include align-items(center);
			border: 0;
			padding: 0;
			span {
				margin-left: 7px;
				font-family: $baseFontFamily;
			}
		}
	}
}
