/*----------  app info  ----------*/

.integrated-app {
	&-container {
		padding: 7px 16px;
		margin-bottom: 10px;
		letter-spacing: normal;
		border-radius: 8px;
		background: $app-white;
		box-shadow: 0 4px 4px rgba($app-black, .01);

		button {
			font-family: $baseFontFamily;
		}

		.empty-state-fd {
			.sub-title {
				color: $inputHoverBorder;
				font-size: $text-normal;
				line-height: $baseLineHeight;
				// padding: 12px 0 10px 0;
			}
		}
	}

	&-header {
		.app-icon {
			width: 16px;
			margin-right: 8px;
		}
	}

	&-info {
		.desc {
			padding: 0;
			margin: 16px 0 8px;
			font-weight: $text-weight-semi-bold;
			line-height: 20px;
			text-align: left;
			border: 0;

			.integration-id {
				padding-left: 5px;
				color: $app-black;
			}
		}

		.field {
			display: inline-block;
			margin-bottom: 8px;
			font-size: $text-normal;

			.name {
				color: $secondary-text-color-2;
			}

			.separator {
				padding: 0 3px;
				font-weight: $text-weight-bolder;
				opacity: .8;
			}
		}
	}
}

/*----------  app detail - sidemodel  ----------*/

.integrated-app-detail {
	.ember-modal-header {
		padding: 0 !important;
	}

	.ember-modal-body {
		height: 100% !important;
		padding-top: 24px !important;
		padding-left: 70px !important;

		.header {
			position: relative;
			font-size: $text-large;
			font-weight: $text-weight-semi-bold;
			line-height: 24px;
			// TODO: remove later (required only for 'legacy embedded'/'port:3000' - #wrap .header from itil.css has override)
			height: initial;
			border: 0;

			.app-icon {
				position: absolute;
				left: -45px;
				width: 32px;
			}
		}

		.caption {
			margin: 4px 0 14px;
		}

		.properties {
			border-radius: 6px;
			background: $background-color-2;
			box-shadow: none;

			.sub-details {
				flex-wrap: wrap;
			}

			.property {
				line-height: 20px;
				flex: 50%;

				.name {
					font-size: $text-normal;
					font-weight: $text-weight-semi-bold;
					text-transform: uppercase;
				}

				.value {
					margin: 4px 0 16px;
					font-weight: $text-weight-normal;
					color: $secondary-text-color-1;
				}
			}

			&.show-less {
				.property:nth-of-type(n+5) {
					display: none;
				}
			}
		}

		.show-all-properties {
			padding: 0;
			font-family: $baseFontFamily;
			font-size: $text-normal;
			font-weight: $text-weight-semi-bold;
			border: 0;

			svg { vertical-align: middle; }
		}

		.conversation-container {
			.conversation {
				margin-top: 40px;

				&-header {
					margin-bottom: 16px;

					.user {
						font-weight: $text-weight-semi-bold;
						line-height: 20px;
					}

					.time {
						margin-top: 4px;
						padding: 0;
						font-size: $text-normal;
						font-style: italic;
					}
				}

				&-content {
					position: relative;
					line-height: 20px;

					.email-icon {
						position: absolute;
						top: 4px;
						left: -35px;
					}

					img {
						max-width: 100% !important;
					}

					b {
						font-weight: bold;
					}
				}

				&-attachments {
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;

					.attachment {
						width: calc(50% - 8px);
						min-height: inherit;
						padding: 10px 12px;
						margin: 8px 0;
						font-size: $text-normal;
						border: 1px solid $border-light-color;
						border-radius: 4px;

						.file-icon {
							float: left;
							width: 29px;
							height: inherit;
							margin-right: 8px;
						}

						.file-name {
							overflow: hidden;
							font-weight: $text-weight-medium;
							white-space: nowrap;
							text-overflow: ellipsis;
							color: $primary-text-color-1;
						}

						.file-size {
							color: $secondary-text-color-2;
						}
					}
				}
			}
		}
	}
}
