.date-time-picker-wrapper-utc {
	max-width: 300px;
}

.slot-availability-container {
	display: inline-flex;

	.slot-cont-left-border {
		width: 2px;
		border-radius: $radius;
		background: $app-warning-state;
		height: 100px;
	}

	#loaner-slots-section {
		height: 100px;
		border-radius: 0;
		background: $app-casablanca-light;
		padding: 20px;
		font-weight: $text-weight-semi-bold;
		display: flex;
		flex-direction: column;
		justify-content: center;

		.choose-slot-section {
			display: flex;
			margin-top: 16px;

			.slots-list {
				position: absolute;
				left: 170px;
				top: -5px;
				background: $app-light-bg;
				border: 0.7px solid $dropdown-content-border;
				box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.06), 0px 1px 14px rgba(24, 50, 71, 0.16);
				border-radius: $radius;
				min-width: 400px;
				padding: 16px 9px;

				p {
					margin-left: 8px;
				}

				ul {
					margin: 12px 9px 0 0;
					max-height: 200px;
					overflow-y: scroll;
					overflow-x: hidden;

					li {
						margin: 2px 2px 6px 2px;

						button {
							background: $background-color-2;
							border-radius: 8px;
							border: none;
							font-weight: $text-weight-normal;
							font-size: $baseFontSize;
							text-align: left;
							width: 362px;
						}
					}
				}
			}
		}
	}
}