.associate-btn .chevron-down-icon {
	width: 16px;
	height: auto;
	vertical-align: middle;
}

.tkt-quick-action {
	.association-dropdown .ember-basic-dropdown-content.ember-basic-dropdown-content--in-place {
		margin-top: 37px;
	}
}

.association-wrapper {
	overflow-x: initial !important;
	position: relative;

	.shimmer-loader .shimmer-loader-card {
		margin: 12px 0 !important;
	}

	.associated-tickets {
		margin-top: 8px;

		.ticket-stats {
			width: 260px;
			margin-top: auto;
			margin-bottom: auto;

			.ticket-stats-value {
				max-width: initial;
			}
		}

		.ticket-info {
			margin-top: auto;
			margin-bottom: auto;
		}
	}

	.empty-tab-content-list-section {
		max-width: 280px;

		.ember-basic-dropdown-trigger {
			color: $linkColor;
			font-weight: $text-weight-medium;
		}
	}

	.article--title {
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
		color: $secondary-text-color-1;
	}

	.new-btn-section {
		display: flex;
		position: absolute;
		right: 0;
		top: 2px;
		font-weight: $text-weight-medium;
	}

	.association-cards--wrapper {
		margin-top: 24px;

		&:first-of-type {
			margin-top: 0;
			.article--title {
				margin-bottom: 16px;
			}
		}
	}

	.association-card {
		.delete--btn {
			width: 16px;
			display: flex;
			align-items: center;
			cursor: pointer;
			background: transparent;
			border: none;
			padding: 0;
			outline: inherit;
		}

		.separator {
			width: 1px;
			height: 38px;
			background: $background-color;
			margin-left: auto;
			margin-top: 4px;
		}

		.item {
			&--img {
				@include flexbox-simple(center, center);
				margin-left: 12px;

				.img-wrap {
					width: 32px;
					height: 32px;
					background: $section-background-blue;
					@include border-style($border-light-blue, 4px);
					@include flexbox-simple(center, center);
				}
			}

			&--info {
				width: 50%;
				@include flexbox-simple(normal, normal);
				flex-direction: column;
				flex: 1 1 auto;
				padding-left: 16px;

				.item--title {
					font-weight: $text-weight-medium;
					margin-top: auto;
					margin-bottom: auto;
				}
			}

			&--details {
				display: flex;
				align-items: center;
				flex-wrap: wrap;
				color: $secondary-text-color-1;

				.shimmer-loader {
					width: 80px;

					.shimmer-loader-item {
						width: auto;
						height: 12px;
						margin: 5px 0 0;
					}
				}
			}

			&--requester-name {
				max-width: 110px;
			}

			&--requester-name,
			&--details .ellipse {
				vertical-align: middle;
			}

			&--stats-detail {
				width: 235px;
				margin-left: auto;
				padding-left: 16px;
				border-left: 1px solid $background-color;
			}

			&--stats-value {
				display: inline-block;
				max-width: 160px;
				padding-left: 16px;
				font-weight: $text-weight-medium;
			}

			&--status,
			&--priority {
				display: flex;
			}

			&--priority {
				padding-top: 2px;

				.priority-value {
					display: flex;
				}
			}

			&--display-number {
				display: inline-block;
				vertical-align: middle;
			}

			&--title-text {
				max-width: 80%;
				display: inline-block;
				vertical-align: middle;
			}
		}

		.priority-color {
			float: left;
			height: 10px;
			width: 10px;
			border-radius: 2px;
			margin: 5px 6px 4px -3px;
		}

		.new-tab {
			vertical-align: middle;
			display: inline-flex;
			margin-left: 4px;

			.app-icon {
				width: 12px;
				height: 12px;
				fill: $app-primary;
			}
		}
	}

	.load-more--infinity-data {
		margin-top: 16px;
		margin-bottom: 16px;
		color: $app-primary;
	}

	.new-ticket-non-empty {
		svg {
			vertical-align: sub;
		}

		.association-dropdown-trigger {
			display: flex;
		}
		.association-dropdown-content {
			li {
				&:not(.disabled) { color: $primary-text-color-1; }
			}
		}
	}
}

.association-dropdown {
	cursor: pointer;

	.dropdown--label {
		font-weight: $text-weight-medium;
		color: $app-primary;
	}

	.ember-basic-dropdown-content {
		width: 236px;
		text-align: left;
	}

	.ember-basic-dropdown-content-primary-menu li {
		border-radius: 4px;
		padding: 6px;
		font-weight: $text-weight-normal;
		cursor: pointer;

		&.disabled {
			opacity: 0.5;
			pointer-events: none;
			cursor: default;
		}

		a {
			color: $app-grey;
		}

		&:hover {
			background: $background-color;
		}
	}

	&.empty-wrapper--dropdown {
		.dropdown--label {
			vertical-align: middle;
		}

		.add-task-icon {
			vertical-align: text-top;
		}
	}
}

.detach-confirm-modal {
	.ember-modal-footer .btn-primary {
		margin-right: 0;
	}
}

.detachAssociation-wrapper {
	.ticket-link {
		font-weight: $text-weight-semi-bold;
		font-size: $title-small;
		max-width: 90%;
		display: inline-block;
	}

	.ticket-desc {
		margin-top: 8px;

		@include line-clamp($lines: 2);
	}

	.ticket-stats {
		display: flex;
		flex-direction: row;
		margin-top: 18px;

		.ticket-priority {
			display: flex;
			position: relative;
			padding-left: 12px;

			&::before {
				content: "";
				border-left: 1px solid $app-border-primary;
				height: 18px;
				margin-top: 1px;
				position: absolute;
				left: 0;
			}
		}

		.ticket-stats-value {
			display: flex;
			padding-left: 12px;
			max-width: 170px;
			font-weight: $text-weight-medium;
		}

		.priority-color{
			@include priority-colors('.priority-1', '.priority-2', '.priority-3', '.priority-4');
		}

		.ticket-status {
			display: flex;
			margin-right: 12px;
		}
	}

	.priority-color {
		float: left;
		height: 10px;
		width: 10px;
		border-radius: 2px;
		margin: 6px 6px 4px 0px;
	}
}

.association-link--table {
	margin-top: 15px;
	border-collapse: separate;
	border-spacing: 0 8px;

	.subject {
		max-width: 230px;
		display: inline-block;
		vertical-align: middle;
	}

	.subject,
	.association-label,
	.ticket-display {
		display: inline-block;
		vertical-align: middle;
	}

	.association-label {
		padding-bottom: 0;
	}

	.new-tab {
		visibility: hidden;
		vertical-align: middle;
		margin-left: 4px;
	}

	.app-icon {
		width: 12px;
		height: 12px;
	}

	thead {
		background: $section-background-blue;

		th {
			padding-top: 12px;
			padding-bottom: 12px;
			font-weight: $text-weight-medium;
			border-top: 1px solid $border-light-blue;
			border-bottom: 1px solid $border-light-blue;

			&:nth-child(1) {
				width: 10px;
				padding-left: 15px;
				padding-right: 15px;
				border-left: 1px solid $border-light-blue;
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px;
			}

			&:nth-child(2) {
				width: 460px;
			}

			&:nth-child(3) {
				width: 150px;
				border-right: 1px solid $border-light-blue;
				border-top-right-radius: 4px;
				border-bottom-right-radius: 4px;
			}
		}
	}

	tbody {
		&::before {
			content: "";
			display: block;
			height: 2px;
		}

		input[type="checkbox"] {
			margin-top: 4px;
		}

		tr {
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			margin-bottom: 8px;

			td {
				padding-top: 12px;
				padding-bottom: 12px;
				border-top: 1px solid $border-light-color;
				border-bottom: 1px solid $border-light-color;

				&:nth-child(1) {
					width: 10px;
					padding-left: 15px;
					padding-right: 15px;
					border-left: 1px solid $border-light-color;
					border-top-left-radius: 4px;
					border-bottom-left-radius: 4px;
				}

				&:nth-child(2) {
					width: 460px;
				}

				&:nth-child(3) {
					width: 150px;
					padding-right: 10px;
					word-break: break-word;
					border-right: 1px solid $border-light-color;
					border-top-right-radius: 4px;
					border-bottom-right-radius: 4px;
				}

				.po-order_number {
					display: block;
					font-size: $text-normal;
				}
			}

			&:hover {
				td {
					border-top-color: $border-light-blue;
					border-bottom-color: $border-light-blue;

					&:nth-child(1) {
						border-left-color: $border-light-blue;
					}

					&:nth-child(3) {
						border-right-color: $border-light-blue;
					}
				}

				.ticket-link {
					color: $app-primary;

					.new-tab {
						visibility: visible;
					}

					.app-icon {
						fill: $app-primary;
					}
				}
			}
		}
	}
}

// TODO:: priority color need to addressed in generic manner
.priority-color {
	&.priority-low {
		background: $priorityLowColor; /* Old browsers */
	}
	&.priority-medium {
		background: $priorityMediumColor;
	}
	&.priority-high {
		background: $priorityHighColor; // Old browsers
	}
	&.priority-urgent {
		background: $priorityUrgentColor; /* Old browsers */
	}
}

.association-modal .ember-modal-header {
	// since the toggle buttons are placed inside header
	.add-or-associate-child-tickets .toggle-buttons {
		padding-top: 30px;
		margin-bottom: -4px;
	}
	
	.sub-title {
		margin: 4px 0 0;
	}
}

.ember-modal-body .new-problem-form-container {
	padding-bottom: 70px;

	.new-problem-form,
	.new-change-form {
		.asset-associate-link {
			font-weight: $text-weight-medium;
			color: $app-primary !important;
			border: none !important;
			box-shadow: none !important;

			svg {
				position: relative;
				top: 3px;
			}
		}

		.requester-field {
			position: relative;
			.add-new-requester-btn {
				position: absolute;
				top: 0;
				right: 0;
				@include inline-flexbox;
				@include align-items(center);
				border: 0;
				padding: 0;
				span {
					margin-left: 7px;
				}
			}
		}

		.requester-field-container {
			display: block;

			.form-field {
				flex: auto;
				max-width: initial;	
			}
		}
	}

	.existing-tickets {
		.search-section {
			padding-top: 0;
		}

		.empty-states {
			text-align: center;
			padding-top: 60px;
		}

		.shimmer-loader .shimmer-loader-card {
			margin: 12px 0;
			height: 50px;

			.shimmer-loader-item {
				margin: 6px;

				&:last-child {
					margin-left: auto;
				}
			}
		}

		.search-section .search-text-box.disabled {
			background: $app-grey-bg;
		}
	}

	.empty-existing-ticket {
		text-align: center;

		p {
			margin-bottom: 4px;

			span {
				font-weight: $text-weight-semi-bold;
			}
		}
	}
}

// Project associations
.existing-project-container {
	.search-section {
		margin-top: 16px;
	}
}

.existing-project--list-wrap {
	margin-top: 16px;
	.list--item {
		display: flex;
		border: 1px solid $border-light-color;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
		border-radius: 4px;
		margin-bottom: 8px;
		padding: 10px 16px;

		.project--name {
			margin-left: 12px;

			.item--title-text {
				max-width: 280px;
				margin-right: 4px;
			}

			&.project--overdue {
				.item--title-text {
					max-width: 220px;
				}
			}

			.app-icon {
				width: 12px;
				height: 12px;
			}

			.item--title-text,
			.association-label,
			.app-icon {
				display: inline-block;
				vertical-align: middle;
			}
		}

		.association-label {
			padding-bottom: 0;
		}

		.item--link {
			color: $primary-text-color-1;
		}

		.project--label {
			background: $section-background-blue;
			font-size: 12px;
			padding: 2px 8px;
			border-radius: 4px;
			margin-left: auto;
		}

		.project--overdue {
			&-count {
				display: flex;
				background: $overdueBg;
				margin-top: auto;
				margin-bottom: auto;
				font-size: $text-normal;
				padding: 2px 8px;
				border-radius: 4px;
				line-height: 1.9;
				margin-left: auto;
			}

			&-label {
				margin-left: 10px;
			}
		}

		&:hover {
			.item--link {
				color: $linkColor;

				.app-icon {
					fill: $linkColor;
				}
			}
		}
	}
}

.project--association-card {
	.item--status-detail {
		display: flex;
		margin-left: auto;
		margin-bottom: auto;
		margin-top: auto;

		.item--issue-type {
			margin: auto 0 auto 12px;
		}

		.separator {
			margin: 4px 16px 0;
			height: 20px;
		}
	}

	.owner-detail {
		.owner-label,
		.owner-value {
			vertical-align: middle;
		}

		.owner-value {
			max-width: 90%;
			display: inline-block;
		}
	}

	.item--overdue {
		&-count {
			display: flex;
			background: $overdueBg;
			margin-top: auto;
			margin-bottom: auto;
			font-size: $text-normal;
			padding: 2px 8px;
			border-radius: 4px;
			line-height: 1.9;
			margin-right: 8px;
		}
	}

	.item--status {
		margin-top: auto;
		margin-bottom: auto;
		font-size: $text-normal;
		padding: 2px 8px;
		line-height: 1.9;
		border-radius: 4px;
		color: $alertCancelColor;
	}

	.status {
		&-yet-to-start,
		&-open {
			background-color: $section-background-blue;
		}

		&-in-progress {
			background-color: $app-casablanca-light;
		}

		&-completed,
		&-done {
			background-color: $alertSuccessBackground;
		}
	}
}

.project--new-form {
	.project-template-info {
		display: -webkit-flex;
		display: flex;
		-webkit-justify-content: space-between;
		justify-content: space-between;
		font-size: $text-normal;
		margin-top: -12px;
		margin-bottom: 16px;

		.project-template--link .app-icon {
			width: 12px;
			height: 12px;
			vertical-align: middle;
			margin-left: 4px;
			fill: $linkColor;
		}
	}

	&.submit-in-progress {
		position: relative;

		// setting opacity fade on immediate child levels, instead of top level, helps keep the blue spinner at 100% opacity
		> * { @include fade-loader; }
		&:after {
			content: '';
			position: absolute;
			top: 230px;
			left: auto;
			right: 50%;
			@include blue-spinner;
		}
	}

	.project-category--wrap {
		margin-top: 0 !important;

		fieldset.form-field {
			margin: 0;
			display: flex;
		}

		.project-category--field {
			width: 260px;
			border: 1px solid $app-border-primary;
			position: relative;
			margin: 0;
			border-radius: 4px;
			margin-right: 16px;

			&:nth-child(2) {
				margin-left: auto;
				margin-right: 0;
			}

			label {
				display: flex !important;
				padding: 16px !important;
				cursor: pointer;
			}

			input[type="radio"] {
				visibility: hidden;
				position: absolute;
				left: 0;
			}

			.content {
				&--title {
					font-size: $title-small;
					color: rgba($app-grey, 0.5);
				}

				p {
					color: $disabled-text-color;
					line-height: 1.4;
					margin: 4px 0 0;
					font-weight: $text-weight-medium;
				}
			}

			.app-icon {
				width: 90px;
				height: 59px;
			}

			&.active {
				border-color: $linkColor;

				.content {
					&--title {
						color: $linkColor;
					}

					p {
						color: $secondary-text-color-1;
					}
				}
			}
		}

		&.project-template--enabled {
			.project-category--field {
				display: none;
				margin: 0;

				&.active {
					display: block;
				}
			}
		}
	}

	.asset-associate-link .app-icon {
		position: relative;
		top: 3px;
	}

	.project-visibility-wrapper {
		border: none;
		padding-left: 4px;
		background: transparent;

		.visibility--icon {
			width: 24px;
			height: 24px;
			background-color: $linkColor;
			color: $app-white;
			border-radius: 50%;
			line-height: 1.8;
			display: inline-block;

			.app-icon {
				fill: $app-white;
				width: 14px;
				height: 14px;
			}
		}

		.visibility--label {
			font-weight: $text-weight-medium;
			margin-left: 4px;
			margin-right: 4px;
			display: inline-block;
		}

		.dropdown-arrow-down {
			top: 3px;
			fill: $btnPrimaryBorder;
		}

		.visible-project--hint {
			margin-left: 8px;

			.app-icon {
				vertical-align: middle;
				fill: $secondary-text-color-2;
			}
		}
	}

	.visibility--dropdown-content {
		&.ember-basic-dropdown-content {
			min-width: 300px;
		}

		input[type="radio"] {
			visibility: hidden;
			position: absolute;
		}

		li {
			margin-bottom: 2px;

			label {
				padding: 14px;
				cursor: pointer;
				display: flex;

				.project-visibility--icon {
					margin-top: 4px;
				}

				.project-visibility--content {
					margin-left: auto;
					width: 88%;
					font-weight: $text-weight-semi-bold;

					p {
						font-weight: $text-weight-normal;
						margin-bottom: 0;
						margin-top: 2px;
						line-height: 1.3;
					}
				}

				&:hover {
					background-color: $dropdown-hover;
					border-radius: 4px;
				}
			}

			&.active {
				border-radius: 4px;
				background-color: $dropdown-hover;

				.project-visibility--content {
					color: $linkColor;
				}
			}
		}
	}

	.form-field .app-icon {
		width: 16px;
		height: 16px;
	}

	.form-date--field-wrap {
		display: flex;

		.form-field {
			width: 48%;

			.date-picker-wrapper {
				max-width: 100%;
			}

			&:nth-child(2) {
				margin: 0;
				margin-left: auto;
			}
		}
	}
}

.new-ticket-form-elements-container {
	.asset-associate-link .app-icon {
		vertical-align: text-bottom;
		margin-right: 2px;
	}
}

// Responsive for card || Temp: It will be removed Once the generic card is implemented
@media (max-width: 1480px) {
	.association-wrapper .association-card .item--title-text {
		max-width: 70%;
	}

	.project--association-card .owner-detail .owner-value {
		max-width: 75%;
	}
}

@media (max-width: 1200px) {
	.association-wrapper .association-card .item--title-text {
		max-width: 60%;
	}
}
