$freddy-expanded-container-box-sharow: 0px 4px 4px rgba(0, 0, 0, 0.01);
.ticket-freddy-suggestions-wrapper {
	border: 1px solid $app-border-primary;
	border-radius: 8px;
	background: linear-gradient(0deg, $background-color-2, $background-color-2);
	.svg-16 {
		height: 16px;
		width: 16px;
	}
	.inverse {
		transform: rotate(180deg);
	}
	.ticket-freddy-suggestions-header {
		padding: 10px 20px;
		height: 48px;
		@include flexbox;
		@include align-items(center);
		.freddy-img {
			height: 32px;
			width: 32px;
		}
		.tkt-freddy-suggestion-title {
			@include flexbox-simple(space-between, center);
			flex: 1;
			margin-left: 14px;
			font-size: $title-normal;
			font-weight: $text-weight-semi-bold;
			color: $app-grey;
			svg {
				fill: $secondary-text-color-1;
			}
		}
	}
	&.expanded {
		box-shadow: $freddy-expanded-container-box-sharow;
		border-radius: 8px;
		border: 1px solid $border-light-blue;
		overflow: hidden;
		.ticket-freddy-suggestions-header {
			border-radius: 8px 8px 0 0;
			border-color: transparent;
		}
		.ui-accordion-panel-wrapper {
			background-color: $white;
		}
	}

	.ticket-freddy-suggestions-body {
		.vertical-navbar {
			max-height: 280px;
			.multitab-navbar {
				height: 100%
			}
			.pane-content {
				overflow-y: scroll;
				height: 100%;
				padding-right: 0;
			}
		}
		.pane-content {
			overflow-y: scroll;
			.tkt-freddy-list-wrapper {
				max-height: 220px;
				overflow-y: scroll;
			}
		}
		.freddy-irrelevant-suggestions {
			padding: 10px 0;
			.freddy-irrelevant-suggestions-btn {
				padding: 0;
				margin: 0;
			}
			.freddy-irrelevant-suggestions-btn,
			.feedback-thanks-text {
				float: right;
				font-size: $text-normal;
				color: $app-form-label-color;
				font-weight: $text-weight-normal;
			}
			.feedback-thanks-text {
				color: $alertSuccessColor;
			}
		}
	}

	.tkt-insert-suggestion-list {
		margin-top: 0;
		.tkt-insert-suggestion-card {
			padding: 8px 16px;
			min-width: 550px;
			.tkt-suggestion-card-data {
				width: 90%;
				.tkt-suggestion-card-title {
					max-width: 99%;
					padding: 0;
				}
			}
			.solution-data {
				width: 85%;
			}
			.info-tooltip-container {
				white-space: nowrap;
			}
		}

		.tkt-freddy-canned-response-card {
			.canned-response-title {
				max-width: 99%;
			}
		}

		.tkt-freddy-similar-incidents-card {
			.similar-incidents-sub {
				max-width: 99%;
				margin-right: 5px;
			}
			.similar-incident-properties {
				@include flexbox-simple(flex-start, center);
				height: 25px;
				margin-left: 2px;
				.similar-incident-status {
					@include flexbox;
					@include align-items(center);
				}
				.similar-incident-status-label {
					max-width: 55px;
				}
				.similar-incident-status-value {
					margin-left: 5px;
					max-width: 105px;
				}
				.similar-incident-status-label,
				.similar-incident-status-value,
				.similar-incident-assign-to-label {
					font-size: $text-normal;
				}
				.similar-incident-status-value,
				.group-agent-result {
					font-size: $text-normal;
					font-weight: $text-weight-medium;
				}
				.similar-incident-assign-to {
					@include flexbox;
					@include align-items(center);
					.similar-incident-assign-to-label {
						max-width: 120px;
						&::before {
							content: '';
							border: 3px solid $app-border-primary;
							border-radius: 50%;
							display: inline-block;
							margin: 0 10px 2px;
						}	
					}
					.agent-group-wrapper {
						max-width: 225px;
						.agent-group-trigger {
							&:hover, &[aria-expanded]{
								box-shadow: none;
								background: $dropdown-hover;
							}
							.dropdown-chevron{
								fill: $secondary-text-color-1;
							}
						}
						.group-agent-result {
							font-size: $text-normal;
							font-weight: $text-weight-medium;
						}
						.group-ellipsis,
						.agent-ellipsis {
							max-width: 100px;
						}
					}
				}
			}
		}
	}
}

.tkt-freddy-insert-solution-modal,
.tkt-freddy-canned-response-modal
{
	svg {
		width: 16px;
		height: 16px;
	}
	.ember-modal-header {
		padding-top: 0 !important;
	}
}

.ticket-freddy-si-summary-modal {
	.ember-modal-body {
		position: relative;
	}
	.ember-modal-footer {
		display: none !important;
	}
}