// there is delay in emptying the html content, so hiding it for ember modules if transition is done swiftly
// https://github.com/freshdesk/itildesk/blob/prestaging/app/views/layouts/maincontent.html.erb#L108
.ember-application-body {
	// legacy breadcrumbs
	div.non-sticky { display: none; }
}

.module-tickets {
	.page-title {
		a {
			@include flexbox-simple(flex-start, center);

			svg {
				width: 15px;
				margin-right: 8px;
				height: 15px;
			}
		}
	}

	.page-layout-content {
		font-family: inherit;
		.page-content {
			margin-bottom: 0 !important;
			min-height: 100vh !important;
			.bulk-action-section {
				#bulk-actions-toolbar{
					.bulk-actions-toolbar-column{
						.shimmer-loader {
							width: 150px;
						}
						.bulk-more-wrapper {
							.bulk-more-trigger {
								width: 32px;
							}
							.bulk-more-content{
								width: 198px;
								ul {
									li {
										margin-bottom: 4px;
										&:focus {
											border: 1px solid $inputFocusBorder;
											border-radius: 4px;
										}
										&:last-of-type {
											margin-bottom: 0px !important;
										}
										.basic-dropdown-item {
											height: 32px;
										}
									}
								}
							}
						}
					}
				}
			}
			.ember-table {
				tr {
					// Grey out table row based on ACL conditions
					// Since there are cell specific exceptions/handlings,
					// this is not done at table scss level.
					&.greyed-out {
						td {
							// Since there are dropdown content, hover card elements, pseudo selectors,
							// not applying `opacity:50%` directly onto it.
							> .status-list-wrapper, > .priority-list-wrapper,
							> .agent-group-wrapper, > .subject-card-wrapper,
							> .requester-card-wrapper, .ui-pills-wrapper {
								opacity: 100% !important;
								.subject-cell {
									opacity: 50% !important;
								}
							}

							&.cell_default_ticket_subject_item .et-cell-container {
								opacity: 100%;
								.subject-cell { opacity: 50% !important; }
							}

							// keep checkbox un-greyed-out
							&.cell_check_box_item {
								opacity: 100%;
								* { opacity: 100% ;}
							}
						}
					}
					&.is-bold {
						.cell_default_ticket_subject_item {
							font-weight: $text-weight-semi-bold;
						}
					}
				}
				td {
					&.cell_default_display_id_item,
					&.cell_default_ticket_subject_item {
						.lt-line-clamp__suffix { // Line clamp addon suffix class
							font-style: normal;
							font-weight: normal;
							font-size: $title-small;
							line-height: 18px;
							color: $secondary-text-color-1;
							white-space: nowrap;
						}
						.subject-cell {
							display: block;
							color: $primary-text-color-1;
							width: 100%;
							&:hover {
								color: $linkColor;
							}
						}
					}
					&.cell_default_ticket_subject_item {
						padding: 0 !important;
						height: inherit !important;
						.subject-card-wrapper {
							display: flex;
							align-items: center;
							height: 100%;
							cursor: pointer;
							padding: 8px 16px;
							& > .screen-reader-only + .ember-view {
								width: 100%;
							}
						}
					}
					&.cell_default_state_item {
						text-transform: capitalize;
						overflow: hidden;
						text-overflow: clip !important;
						@extend .ticket-state-badge;
					}
					&.cell_custom_checkbox_item {
						text-transform: capitalize;
					}
					&.cell_custom_multi_select_dropdown_item {
						.ui-pills {
							margin: 0 8px 0 0;

							&.hint--right:after {
								content: " ";
								display: none;
							}
							&:hover:after {
								content: attr(aria-label);
								display: block;
								margin-left: -10px;
							}
						}
						&:hover {
							z-index: 5;
						}
					}
					.tooltip-wrapper {
						@extend .ellipsis;
					}
				}
			}
		}
	}

	.ember-tooltip {
		z-index: 99;
	}

	.ui-component-refresh-notification {
		margin-top: -1px;
	}

	.ember-table-wrapper {
		&	> .table-options {
			padding: 0 70px 0 32px;
			margin-left: -32px;
			z-index: 9;
			transition: top .2s ease-in-out;
		}

		.ember-table-wrap.compact .subject-card-wrapper {
			padding: 0 16px !important;
		}
	}

	.tickets-table-wrapper {
		&.padding-right {
			.table-options {
				padding-right: 32px;
			}
		}
		&.has-info-banner { // adjust rts bubble position
			.ui-component-refresh-notification {
				margin-top: 55px;
			}
		}
	}

	.page-sidebar {
		z-index: 9;
		position: absolute;
		right: 0;
	}

	#view-query-form-trigger {
		position: fixed;
		top: 64px; // initial value, dynamic value set later in code based on a window event
		z-index: 9;
		right: 32px;
		margin-right: 0;
		margin-top: 14px;
		background: $app-white;
		& > * {
			transition: top .2s ease-in-out;
		}
		&:focus {
			outline: none;
			box-shadow: 0 0 1px 1px $inputFocusBorder !important;
		}
		&.active {
			right: 332px;
			background: $section-background-blue;
			border: 1px solid $inputFocusBorder !important;
			border-radius: $inputBorderRadius;
			box-shadow: none;
			svg path {
				fill: $inputFocusBorder;
			}
		}
	}



	.page-content-with-sidebar {
		.ui-component-refresh-notification, .notification-with-close-wrapper {
			left: calc(50% - 100px);
		}
	}
}

.flash-message.alert {
	.bulk-warning-text {
		color: $app-red-1;
	}
	.bulk-failed-modal {
		cursor: pointer;
	}
}
