$ticketCardTooltipShadow: 0px 2px 4px rgba(18, 52, 77, 0.16), 0px 2px 16px rgba(18, 52, 77, 0.06);

.tkt-insert-suggestion-list {
	margin-top: 24px;
	.tkt-insert-suggestion-action {
		display: none;
	}
	.solution-card-wrapper,
	.tkt-insert-suggestion-card {
		@include flexbox-simple(space-between, center);
		padding: 16px;
		margin-bottom: 8px;
		background: $white;
		border: 1px solid $border-light-color;
		border-radius: 8px;
		box-shadow: $card-box-shadow;
		.solution-data,
		.canned-response-data {
			width: 85%;
		}
		&:hover {
			border: 1px solid $linkColor !important;
			.tkt-insert-suggestion-action {
				display: flex;
			}
		}
		.tkt-insert-suggestion-card-tooltip {
			word-break: break-word;
			max-width: 350px;
			background: $app-light-bg !important;
			color: $primary-text-color-1 !important;
			padding: 12px 16px;
			box-shadow: $ticketCardTooltipShadow;
		}
	}
	.tkt-suggestion-card-title {
		font-weight: $text-weight-semi-bold;
		padding: 0;
	}
	.solution-tag-title {
		max-width: 85%;
	}
	.solution-tag-draft {
		color: $alertCancelColor;
	}
	.tkt-suggestion-card-tag {
		@include flexbox-simple(center, center);
		margin: 0 5px 0 3px;
	}
	.solution-category,
	.canned-response-folder {
		font-weight: $text-weight-medium;
		font-size: $text-normal;
		color: $secondary-text-color-1;
		@include flexbox;
		@include align-items(center);
		margin: 5px 0 0 3px;
		.folder-icon {
			width: 12px;
			height: 12px;
			fill: $disabled-text-color;
			margin-right: 5px;
		}
		.folder-name {
			max-width: 90%;
		}
	}
	.insert-suggestion-btns {
		background: $white;
		padding: 4px 6px;
		@include flexbox;
		@include align-items(center);
		margin: 0 0 0 8px;
		&:hover {
			background: $background-color-2 !important;
		}
		svg {
			fill: $secondary-text-color-1;
			path {
				fill: $secondary-text-color-1;
			}
		}
	}
}

.tkt-suggestion-details-wrapper {
	.tkt-suggestion-details-title {
		font-weight: $text-weight-semi-bold;
	}
	.tkt-suggestion-details-desc {
		padding: 16px 0;
		img {
			width: 100%;
		}
	}
	.tkt-sugg-details-attachment {
		.tkt-sugg-attachments-title {
			font-size: $text-normal;
			font-weight: $text-weight-medium;
			@include flexbox;
			@include align-items(center);
			.attachment-tooltip-wrapper {
				@include flexbox;
				.tkt-sugg-attachments-icon {
					width: 14px;
					height: 14px;
					fill: $linkColor;
					margin-left: 5px;
				}
			}
		}
		.attach-files {
			padding: 0;
			margin: 0;
			background: $white;
			.attachment-list {
				@include flexbox;
				flex-wrap: wrap;
				.list-file-item {
					@include flexbox;
					height: 50px;
					padding: 12px;
					width: 217px;
					margin: 8px 8px 0 0;
				}
			}
		}
	}
}

.sol-details-insert-url-btn,
.sol-details-insert-content-btn,
.cr-details-insert-cr-btn {
	@include flexbox;
	@include align-items(center);
	svg {
		margin-right: 8px;
	}
}

.tkt-freddy-solution-list-wrapper,
.tkt-freddy-cr-list-wrapper {
	padding: 16px 0;
	margin-bottom: 24px;
	border-bottom: 1px solid $border-dark-color;
	.tkt-freddy-solution-list-header,
	.tkt-freddy-cr-list-header {
		@include flexbox;
		@include align-items(center);
		.tkt-freddy-suggestion-title {
			margin-left: 15px;
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
		}
	}
	.freddy-irrelevant-suggestions {
		@include flexbox-simple(end, center);
		.freddy-irrelevant-suggestions-btn,
		.feedback-thanks-text {
			font-size: $text-normal;
		}
		.feedback-thanks-text {
			color: $alertSuccessColor;
		}
	}
}

.tkt-freddy-cr-list-wrapper {
	padding-top: 0;
	.tkt-freddy-cr-list-header {
		padding-bottom: 24px;
	}
}