.child-tickets-wrapper {
	.new-btn-section {
		margin: 0 1px 0 0;
		.new-ticket-non-empty {
			@include flexbox-simple(normal, center);
			background: none;
			border: none;
			cursor: pointer;
			font-weight: $text-weight-medium;
			margin-left: auto;
			padding-right: 0;
			span {
				line-height: 17px;
				padding-left: 5px;
			}
			svg {
				margin-left: auto;
				margin-top: 0;
			}
		}
	}

	.new-ticket {
		display: flex;
		margin-top: 20px;
		margin-left: 8px;
		border: none;
		background: none;
		cursor: pointer;
		span {
			font-weight: 500;
			font-size: 14px;
			color: $inputFocusBorder;
			line-height: 17px;
			padding-left: 5px;
		}
		svg {
			width: 16px;
			height: 16px;
		}
	}
	.associated-tickets{
		.subheading {
			color: $secondary-text-color-1;
			font-weight: 400;
		}
		.delete-img {
			display: flex;
			align-items: center;
			width: 16px;
			cursor: pointer;
			background: none;
			color: inherit;
			border: none;
			padding: 0;
			font: inherit;
			outline: inherit;
			&:focus {
				border: 1px solid black;
			}
			svg {
				height: 16px;
				width: 16px;
			}
		}
		.ticket-img {
			display: flex;
			align-items: center;
			justify-content: center;
			width:32px;
			height: 32px;
			background: $section-background-blue;
			border: 1px solid $border-light-blue;
			box-sizing: border-box;
			border-radius: 4px;
			margin-left: 13px;
			margin-top: 3px;
		}
		.ticket-info {
			padding-left: 16px;
			display: flex;
			flex-direction: column;
			width: 480px;
			flex: 1 1 auto;
			.ticket-details {
				display: flex;
				align-items: center;
				line-height: 17px;
				flex-wrap: wrap;
				.ticket-requester-name {
					max-width: 110px;
				}
			}
			.subject {
				width: 284px;
				display: flex;
				padding-bottom: 2px;
				line-height: 17px;
				.ticket-display{
					max-width: 80px;
				}
				.ticket-link{
					display: flex;
					padding-left: 4px;
					font-weight: 500;
					line-height: 17px;
				}
				a:hover {
					outline-offset: -1px !important;
				}
				a:focus {
					outline-offset: -1px !important;
				}
			}
			.new-tab {
				fill: $linkColor;
				svg {
					width: 12px;
					height: 12px;
					margin: 2px 0 0 5px;
				}
			}

		}
		.separator{
			height: 38px;
			width: 	1px;
			border-radius: 2px;
			background: $border-light-color;
			margin-top: 4px;

		}
		.ticket-stats {
			@include flexbox-simple(normal, normal);
			flex-direction: column;
			padding-left: 16px;
			width: 150px;
			margin-right: 64px;
			.ticket-priority {
				display: flex;
				padding-top: 4px;
			}
			.ticket-stats-value {
				padding-left: 21px;
				max-width: 90px;
			}
			.ticket-status{
				display: flex;
			}
		}
		.priority-color{
			float: left;
			margin: 4px 6px 4px -3px;
			@include priority-colors('.priority-1', '.priority-2', '.priority-3', '.priority-4');
		}
		.ticket-state {
			margin-left: auto;
			display: flex;
			align-items: center !important;
			justify-content: center;
		}
		.subject-style {
			border-radius: 4px;
			font-weight: 600;
			text-transform: uppercase;
			display:flex;
			white-space:nowrap;
			cursor: default !important;
			font-size: 12px;
			line-height: 14px;
			margin-right: 8px;
			.state-style {
				padding: 3px 8px !important;
				border-radius: 4px;
			}
			@include subject-style();
		}
	}
	.new-btn-section + .related-tickets {
		margin-top: -18px;
	}
	.related-tickets, .predecessor-ticket, .successor-tickets {
		p {
			font-weight: $text-weight-semi-bold;
			color: $secondary-text-color-1;
			margin-bottom: 12px;
		}
	}
	.related-tickets p { margin-bottom: 16px; }
	.eob-related-tickets {
		.predecessor-ticket, .successor-tickets {
			margin-top: 24px;
		}
	}
}
.add-or-associate-child-tickets {
	.toggle-buttons{
		@include flexbox-simple(flex-start, center);
		padding-top: 10px;
		margin-bottom: 16px;
		.rd-existing-btn {
			margin-left: 64px;
		}
		label {
			padding-left: 8px;
			padding-bottom: 0;
			font-size: $title-small;
			line-height: 20px;
		}
		input {
			width: 14px;
			height: 14px;
		}
	}
	.existing-tickets {
		.search-text {
			padding-top: 18px;
			padding-bottom: 5px;
		}
		.search-section{
			display: flex;
			flex-direction: column;
			.search-text-box {
				width: 536px;
				height: 32px;
				background: $white;
				border: 1px solid $app-border-primary;
				border-radius: 4px;
				display: flex;
				input {
					border: none;
					width: 506px;
					box-sizing: border-box;
					height: 30px;
					box-shadow: none;
					font-family: inherit;
				}
				input:focus {
					outline: none;
					-webkit-box-shadow: none;
					box-shadow: none;
				}
				svg {
					width: 21px;
					height: 23px;
					padding-top: 8px;
					padding-left: 8px;
					fill: $app-form-label-color;
				}
			}
		}
		.search-results {
			.service-request-card{
				width: 527px;
				height: 70px;
				border: 1px solid $border-light-color;
				box-sizing: border-box;
				box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
				border-radius: 8px;
				background: $white;
				display: flex;
				@include flex-center;
				margin-top: 8px;
				padding: 16px 23px 15px 19px;
				&:hover{
					border: 1px solid $border-light-blue;
					box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
				}
				.ticket-checkbox{
					border: 1px solid $checkboxHoverBorder;
					box-sizing: border-box;
					border-radius: 2px;
				}

				.ticket-requester-name {
					max-width: 120px;
				}
				.child-checkbox {
					padding-top: 5px;
				}

				.mid-section{
					display: flex;
					padding-left: 17px;
					width: 75%;
					.ticket-info {
						width: 100%;
					}

					.ticket-display {
						font-weight: 500;
						font-size: 14px;
						color: $primary-text-color-1;
					}
					.subject {
						display: flex;
					}
					.ticket-details {
						font-weight: 400;
						letter-spacing: 0px;
						text-align: left;
						color: $secondary-text-color-1;
						display: flex;
						align-items: center;
					}
					.subject-text {
						font-weight: 500;
						font-size: 14px;
						color: $linkColor;
						max-width: 280px;
						display: block;
						padding-left: 4px;
					}
				}
				.ticket-status{
					width: 20%;
					margin-left: auto;
					align-items: center;
					color: $primary-text-color-1;
					font-weight: 500;
				}
			}
		}
	}
}
.ellipse{
	height: 4px;
	width: 4px;
	left: 317px;
	border-radius: 50px;
	background: $app-border-primary;
	opacity: 0.8;
	margin-left: 8px;
	margin-right: 8px;
}
.truncate {
	white-space: nowrap;
	overflow: hidden;
	text-overflow: ellipsis;
}

// Slider configs
.ember-modal-overlay .child-tickets-add-modal {
	.form-field > label {
		@include line-clamp;
		width: 100%;
	}
	.ember-modal-footer{
		.btn-primary{
			background: $btnDisabledGradientColor;
			border: 1px solid $btnPrimaryBorder;
		}
	}
}
