@import './tasks-show';
@import './tasks-form';

.task-option-style {
	background-color: transparent;
	border: none;
	text-align: left;
	border-radius: 4px;
	width: 100%;
}

#ticket-task-tab {
	.task-list-container {
		width: calc(100% + 20px);
		.task-list {
			width: calc(100% - 20px);
			.task-item {
				.task-icon {
					align-self: flex-start;
					margin-right: 12px;
					margin-left: -4px;
					margin-top: 4px;
					padding: 4px;
					cursor: pointer;
					&.open-task {
						.completed-task-icon {
							display: none;
						}
						&:hover {
							.completed-task-icon {
								display: block;
							}
							.open-task-icon {
								display: none;
							}
						}
					}
					&:focus {
						outline: 2px solid $inputFocusBorder;
					}
				}
				.ellipsis {
					text-overflow: ellipsis;
					overflow: hidden;
					white-space: nowrap;
				}
				.task-id {
					color: $primary-text-color-1;
					font-weight: $text-weight-semi-bold;
				}
				.subject {
					font-weight: $text-weight-medium;
					padding: 0 5px;
					max-width: 350px;
					text-overflow: ellipsis;
					white-space: nowrap;
					overflow: hidden;
				}
				.overdue {
					padding: 0 8px;
					border: 1px solid $alertWarningBorder;
					background: $alertWarningBackground;
					color: $btnDangerBackgroundHighlight;
					border-radius: $inputBorderRadius;
					font-weight: $text-weight-semi-bold;
					font-size: $text-normal;
					height: 24px;
				}
				.task-content {
					width: 65%;
					margin-right: 16px;
				}
				.main-content {
					display: flex;
					align-items: center;
					@extend .ellipsis;
				}
				.task-details {
					color: $app-form-label-color;
					display: flex;
					flex-wrap: wrap;
					.user {
						max-width: 100%;
						@extend .ellipsis;
						&::after {
							content: '\A';
							width: 4px;
							height: 4px;
							border-radius: 50%;
							background: $border-dark-color;
							display: inline-block;
							margin: 0px 8px 2px;
						}
					}
					.due {
						margin-left: 4px;
					}
				}
				.status {
					width: 100px;
					line-height: 2.5;
					margin-left: auto;
					text-align: right;
					color: $primary-text-color-1;
					font-weight: 400;
					padding-right: 26px;
					@extend .ellipsis;
				}
				.task-option-menu {
					width: 32px;
					padding-left: 16px;
					margin-right: 16px;
					display: inline-block;
					border-left: 1px solid $border-light-color;
					.task-more-btn {
						padding: 4px 8px;
						box-shadow: none;
						line-height: 24px;
						background: $white;
						&:hover, &:focus {
							background: $background-color-2;
						}
						svg {
							width: 14px;
							height: 12px;
							fill: $app-grey;
						}
					}
					.ember-basic-dropdown-trigger {
						width: 32px;
					}
				}
			}
			.ember-tooltip  {
				.ember-tooltip-arrow {
					display: none;
				}
			}
			.infinity-loader {
				&.reached-infinity {
					display: none;
				}
			}
		}
		.recently-added-wrap {
			padding-bottom: 16px;

			.title {
				font-size: $text-normal;
				font-weight: $text-weight-semi-bold;
				color: $secondary-text-color-1;
			}

			.recently-added-list .list {
				margin-top: 4px;
				margin-bottom: -16px;
				padding-bottom: 16px;
			}
		}
		.recent-task-border-line {
			.list {
				border-bottom: 1px dashed $border-dark-color;
			}
		}
	}
	.tasks-filter {
		display: inline-block;
		color: $secondary-text-color-1;
		font-weight: $text-weight-semi-bold;
		min-width: 250px;
		text-align: left;
		.filter-icon {
			vertical-align: middle;
		}
		.task-filter-menu {
			width: 250px;
			.task-filter-item {
				@extend .task-option-style;
				&:hover {
					background: $border-light-color;
				}
				&.active {
					background-color: $section-background-blue;
					color: $linkColor;
					font-weight: $text-weight-medium;
					&:after {
						top: 5px;
					}
				}
			}
		}
	}
	.add-task-header {
		float: right;
		.add-task-btn {
			@include flexbox;
			@include align-items(center);
			font-weight: $text-weight-medium;
		}
	}
	.task-header {
		margin: 0 0 16px;
	}
	.empty-tab-content-list-section {
		margin: 100px auto;
	}
}

.task-dropdown-menu {
	padding: 4px;
	width: 198px !important;
	> .task-option-item {
		padding: 8px 12px;
		margin: 2px 0;
		color: $primary-text-color-1;
		@extend .task-option-style;
		&:hover {
			background: $border-light-color;
		}
	}
}

.task-modal #task-show .more-details .due_date-container, .task-list-container {
	.task-warning-container, .content {
		display: flex;
		align-items: center;
	}
	.task-waning-icon {
		height: 16px;
		width: 16px;
		cursor: pointer;
		fill: $svg-orange;
		margin-left: 4px;
	}
	.info-tooltip-container {
		text-align: center;
	}
}

.task-fields-ember-form [formserv-field-name='plannedEffort'] input {
	width: 317px !important;
}

.task-modal {
	.title-btns-container svg {
		width: 16px;
		height: 16px;
		vertical-align: middle;
	}
	&.show {
		.header-container {
			display: flex;
			justify-content: space-between;
			.title-btns-container {
				display: flex;
				.btn-white {
					border: 1px solid $border-dark-color;
					background: $white;
					&:hover {
						background: $background-color-2;
					}
				}
				.task-option-menu {
					.task-more-btn {
						width: 32px;
						padding: 3px;
					}
				}
				.page-nav-wrap {
					.page-nav-text {
						margin-right: 0;
					}
					svg {
						vertical-align: text-bottom;
					}
				}
			}
		}
	}
	&:not(.show) {
		.ember-power-select-status-icon {
			right: 12px;
		}
	}
	#task-modal-footer {
		flex: 1;
		align-items: center;

		&-left-content {
			padding-left: 8px;
			input {
				margin: 0 8px 2px 0 !important;
				border-color: $checkboxDefaultBorder;
				&:hover { border-color: $checkboxHoverBorder; }
				&:focus, &:checked, &:active { border-color: $checkboxFocusBorder; }
				&:checked:focus, &:checked:active { border-color: $white; }
			}
		}

		&-right-content {
			margin-left: auto;
			.task-update:disabled {
				border: 1px solid $disabled-button-border;
				background: $disabled-button-bg;
				color: $white !important;
				opacity: 1;
			}
		}

	}
}
