#ticket-requested-items-tab{
	.requested-items-header{
		display: flex;
		justify-content: space-between;
		margin-bottom: 16px;
		.title{
			font-weight: $text-weight-semi-bold;
			font-size: $title-normal;
			line-height: 21px;
			@extend .flex-center;
			.requested-items-count{
				@extend .flex-center;
				font-size: $text-normal;
				color: $white;
				background: $app-primary;
				box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
				border-radius: 20px;
				height: 18px;
				margin-left: 7px;
				width: 26px;
			}
		}
		.add-requested-item-header{
			.btn-link{
				display: flex;
				align-items: center;
				border-width: 0;
				font-weight: $text-weight-medium;
				svg {
					margin-right: 5px;
				}
			}
		}
	}
	.requested-items-list-container{
		margin-bottom: 32px;
		.requested-item-card{
			border: 1px solid $border-light-color;
			box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.05);
			border-radius: 8px;
			margin-top: 12px;
			justify-content: space-between;
			flex-wrap: wrap;
			.card-actions{
				margin-right: 18px;
				.more-btn{
					min-width: 32px;
					.ember-basic-dropdown-trigger {
						margin: 0;
					}
					.req-item-more-dropdown {

						li.basic-dropdown-item {
							padding: 0;
							margin: 0;
						}

						.btn-no-bg {
							display: flex;
							align-items: center;
							width: 100%;
							cursor: inherit;
							padding: 8px 12px;
							max-height: 32px;
							color: $primary-text-color-1;
							svg {
								margin-right: 8px;
								fill: $app-grey;
							}
						}
					}
				}
				.fulfill-btn{
					.btn{
						@extend .flex-center;
						padding: 10px;
						svg{
							margin-right: 7px;
						}
					}
				}
			}
			.more-info{
				flex-basis: 100%;
				background: $background-color-2;
				border-bottom-left-radius: 7px;
				border-bottom-right-radius: 7px;
				padding: 16px;
				border-top: 1px solid $border-light-color;
				.loader-container {
					svg {
						height: 28px;
					}
					.loading-text {
						font-size: $text-normal;
					}
				}
				.cancellation_reasons{
					margin-bottom: 6px;
				}
				&.more-info-expanded{
					&-false{
						display: none;
					}
					&-true{
						display: block;
					}
				}
				.title{
					font-weight: $text-weight-semi-bold;
					line-height: 21px;
					margin-bottom: 4px;
				}
				.req-item-custom-fields-section{
					ul li:first-child{
						margin-top: 28px
					}
					.cf-title{
						font-weight: $text-weight-semi-bold;
						font-size: $text-normal;
						line-height: 20px;
						color: $app-form-label-color;
						margin-top: 18px;
					}
					.cf-value{
						font-weight: $text-weight-medium;
						font-size: $baseFontSize;
						line-height: 21px;
						&.comma-separated-array-contents{
							span:not(:last-of-type):after {
								content: ',';
							}
						}

						&.custom_paragraph {
							word-break: break-all;
							white-space: pre-line;
						}
					}
					.cf-nested{
						border-left: 1px solid $app-border-primary;
						padding: 8px 0 3px 16px;
						&--1{
							margin-left: 16px;
						}
						.cf-title{
							margin-top: 0;
						}
					}
				}
			}

			> div.ember-view:last-child {
				display: none;
			}
		}
		.requested-item-empty-state{
			margin-top: 120px;
			padding-bottom: 40px;
			.empty-state-image, .empty-state-text, .add-item{
				display: flex;
				justify-content: center;
			}
			.empty-state-text{
				padding-top: 7px;
			}
			.add-item{
				padding-top: 12px;
				.btn-link {
					display: flex;
					align-items: center;
					svg {
						margin-right: 5px;
					}
				}
			}
		}
	}
}


#ticket-requested-items-tab, .ember-modal-dialog.req-item-edit {
	.card-info {
		margin: 18px 16px 16px 16px;
		flex: 1;
		.image-section{
			width: 40px;
			height: 40px;
			background: $section-background-blue;
			padding: 5px;
			border-radius: 4px;
			@extend .flex-center;
			.image-wrapper{
				width: 32px;
				height: 32px;
				border-radius: 4px;
				@extend .flex-center;
			}
		}
		.item-card-data{
			flex: 1;
			flex-direction: column;
			margin-left: 16px;
			.card-title{
				font-weight: $text-weight-semi-bold;
			}
			.card-data-props{
				font-size: $text-normal;
				flex-wrap: wrap;
				align-items: baseline;

				.with-dot-separator::after{
					content: "";
					background: $dotLineSeparatorBg;
					border-radius: 50px;
					width: 4px;
					height: 4px;
					margin: 0 9px 2px 9px;
					display: inline-block;
				}

				.with-divider-separator::after{
					content: "";
					border-right: 1px solid $border-light-color;
					height: 24px;
					margin: 0 16px;
				}

				.stage-prop {
					max-height: 20px;
				}

				.data-title{
					color: $secondary-text-color-1;
					line-height: 27px;
				}

				.data-value {
					font-weight: $text-weight-medium;
				}
				.more-info-btn{
					padding: 0;
					font-size: $text-normal;
					line-height: 18px;
					border: 0;
				}
			}
			.is-loaner{
				display: inline-flex;
				align-items: center;
				flex-basis: 100%;
				margin-top: 4px;
				div {
					max-height: 20px;
				}
				.more-info-btn{
					margin-top: 6px;
				}
			}
		}
	}


	.req-stage-dropdown {
		display: inline-block;
		cursor: pointer;
		.req-stage-trigger {
			svg {
				position: relative;
				top: 4px;
			}
		}
		.req-stage-content {
			padding: 0 !important;
			.req-stage-option-list {
				max-height: 300px;
				overflow-y: auto;
				padding: 8px;

				.stage-option-text {
					word-wrap: break-word;
					font-size: $baseFontSize;
				}
			}
		}
	}
}

.ember-modal-dialog.req-item-edit {

	@import "../catalog/service-request/qty-btns";

	.card-info {
		margin: 16px 0;
	}

	.service-request-custom-fields-form {
		margin-top: 24px;

		.form-field {
			margin-top: 16px;
			margin-bottom: 0;
		}

		.static-rich-text-field img {
			max-width: 100%;
		}

		@import "../catalog/service-request/slot-availability";
		@import "../catalog/service-request/nested-child";
		.slot-availability-container #loaner-slots-section .choose-slot-section .slots-list {
			left: 0;
			top: inherit;
		}

		.checkbox-field {
			margin: 18px 0;
			label {
				margin-left: 0;
			}
		}
	}

	.no-fields {
		padding: 32px;
		text-align: center;

		p {
			margin: 24px 0;
			color: $secondary-text-color-1;
			font-size: $title-normal;
		}
	}

	.loader-container {
		margin-top: 64px;

		.ember-loader {
			width: 32px;
			height: 32px;
		}
	}
}
