.new-page-related-articles {

	&.normal-panel {
		background: $app-white;
		border: 1px solid $border-light-blue;
		box-sizing: border-box;
		box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.01);
		border-radius: 8px;
		padding: 16px;
		min-height: 620px;
		width: auto;
		margin: 16px 16px 24px;
	}

	.ui-accordion-item {
		&.ui-accordion-item-expanded {
			min-height: 450px;
		}

		.related-articles-accordion-header-btn {
			display: flex;
			justify-content: space-between;
			align-items: center;
			width: 100%;
			max-width: 334px;
			border: none;
			height: 24px;
			.title {
				font-weight: $text-weight-semi-bold;
				font-size: $title-small;
				display: flex;
				align-items: center;
				color: $primary-text-color-1;
			}
		}
	}

	.sidepanel-title {
		font-weight: $text-weight-semi-bold;
		font-size: $baseFontSize;
		padding-bottom: 8px;
		color: $primary-text-color-1;
	}

	.initial-state-container{
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 92px;
		* { animation: fadeIn 0.25s; }
		span {
			text-align: center;
			width: 85%;
			color: $app-form-label-color;
			padding-top: 30px;
		}
	}

	.related-article {
		border-bottom: 1px solid $background-color-2;
		padding: 16px 0;
		animation: fadeIn 0.25s;
		&:last-of-type { padding-bottom: 8px; }

		a {
			font-size: $baseFontSize;
			color: $primary-text-color-1;

			.article-title {
				display: flex;

				.line-clamp--2-line {
					width: calc(100% - 16px);
					font-weight: $text-weight-semi-bold;
				}
			}

			svg {
				fill: $linkColor;
				display: none;
			}

			&:hover, &:focus {
				color: $inputFocusBorder;
				svg {
					display: inline-block;
				}
			}
		}

		&:last-child {
			border: none;
		}

		.description {
			color: $app-form-label-color;
			padding-top: 10px;
			margin-bottom: 0;
		}

		.line-clamp--2-line {
			display: -webkit-box;
			-webkit-line-clamp: 2;
			-webkit-box-orient: vertical;
			overflow: hidden;
			text-overflow: ellipsis;
		}
	}
}
