#task-form {
	margin-top: 8px;
	margin-bottom: 10px;
	.form-field {
		margin: 24px 0px;
		&:first-child {
			margin-top: 0;
		}

		> label {
			margin-bottom: 4px;
		}

		.ember-power-select-trigger .ember-power-select-selected-item {
			max-width: 500px;
		}
	}
	.froala-editor-container{
		.fr-toolbar {
			border-bottom: none;
			background: $white;
			padding-left: 2px;
		}
		.fr-element {
			padding: 10px 0 !important;
			min-height: 139px !important;// hack to fix scroll bar appearance
		}
		.fr-second-toolbar {
			display: none;
		}
		&.focus {
			.fr-second-toolbar {
				display: none;
			}
		}
	}
}
