.associate-asset-modal {
	.ember-modal-body {
		padding-bottom: 0 !important;
	}
	svg {
		width: 16px;
		height: 16px;
		vertical-align: middle;
	}
	.asset-filter-search-section {
		@include flexbox;
	}
	.asset-type-filter {
		width: 47%;
		.ember-power-select-trigger {
			border: 1px solid transparent !important;
			width: 70%;
			overflow-x: hidden;
			white-space: nowrap;
			text-overflow: ellipsis;
			padding-right: 10px;
			.ember-power-select-status-icon {
				position: relative !important;
				left: 10px;
				right: auto;
				top: -1px;
				margin-left: 5px;
				margin-right: 10px;
			}
			&:focus,
			&:hover,
			&:focus-visible {
				border: 1px solid transparent !important;
			}
			&[aria-disabled=true] {
				background: $white;
				.ember-power-select-selected-item  {
					color: $secondary-text-color-2;
				}
				.ember-power-select-status-icon {
					border-color: $secondary-text-color-2;
				}
			}
		}
		.ember-basic-dropdown-content {
			.search-container {
				top: 10px;
			}
		}
	}
	.asset-search {
		width: 53%;
		.search-box {
			@include flexbox;
			.search-icon {
				position: relative;
				top: 8px;
				left: 30px;
				fill: $app-form-label-color;
			}
			.search-text-box {
				padding-left: 40px;
				font-family: $baseFontFamily;
			}
		}
	}
	.asset-card {
		@include flexbox;
		background: $white;
		border: 1px solid $border-light-color;
		box-sizing: border-box;
		box-shadow: $list-cards-box-shadow;
		border-radius: 4px;
		padding: 9px 16px;
		margin-top: 8px;
		.select-asset,
		.select-all-asset {
			margin-right: 5px;
			&.asset-single-select {
				visibility: hidden;
			}
			input[type="checkbox"] + label,
			input[type="radio"] + label
			{
				visibility: hidden;
				position: absolute;
			}
		}
		.asset-checkbox,
		.asset-radio-btn {
			top: -1px;
		}
		.asset-name,
		.asset-used-by,
		.asset-location {
			font-size: $text-normal;
			padding: 0 5px;
			word-break: break-all;
		}
		.asset-name {
			width: 45%;
			
		}
		.asset-used-by,
		.asset-location {
			width: 25%;
		}
	}
	.asset-list-header {
		border: 1px solid $border-light-blue;
		background: $section-background-blue;
		font-weight: $text-weight-semi-bold;
		margin-top: 15px;
		&.asset-card {
			padding: 7px 16px;
		}
	}
	.ember-pagination-new {
		border-top:1px solid transparent;
		.btn[disabled] {
			color: $disabled-text-color !important;
		}
	}
}