/**
 Associating new incident/ existing incident with alerts in side modal
 */

.incident-association-modal {
	.search-input-wrapper {
		margin: 16px 0 32px 0;

		.search-clear {
			svg {
				display: block;
			}
		}
	}

	.section-header {
		color: $secondary-text-color-1;
		font-size: $text-normal;
		font-weight: $text-weight-medium;
		margin-bottom: 8px;
		text-transform: uppercase;
	}

	.no-results {
		margin-top: 80px;
		text-align: center;

		img {
			display: block;
			height: 96px;
			margin: auto auto 8px auto;
			width: 96px;
		}

		span {
			color: $secondary-text-color-1;
		}
	}

	.dot-loading {
		text-align: center;
	}

	.toggle-radio-fields {
		margin-bottom: 16px;

		legend { border: none; }

		.radio-field {
			border: 1px solid transparent;
			box-sizing: border-box;
			border-radius: 24px;
			padding: 5px 10px;
			display: inline-flex;
			align-items: center;

			label,
			input {
				display: inline-block;
				padding-bottom: 0;
				cursor: pointer;
			}

			input {
				margin-right: 8px !important;
			}
		}

		&.existing-incident .radio-group {
			div:first-of-type {
				background: $section-background-blue;
				border-color: $border-light-blue;
			}
		}

		&.new-incident .radio-group {
			div:last-of-type {
				background: $section-background-blue;
				border-color: $border-light-blue;
			}
		}
	}
}

.incident-association-modal,
.alert-associated-incident {
	.incident-card {
		display: flex;
		align-items: center;
		padding: 5px 10px;
		border: 1px solid $border-light-color;
		box-sizing: border-box;
		box-shadow: 0px 1px 6px rgba(22, 29, 37, 0.07);
		border-radius: 4px;
		width: 100%;
		margin-bottom: 12px;
		outline: none;
		min-height: 72px;

		.btn {
			margin: 0;
			.incident-card-icon-inactive,
			.incident-card-icon-active {
				width: 14px;
				height: 14px;
			}

			.incident-card-icon-inactive {
				fill: $border-dark-color;
				&:hover {
					fill: $svg-icon-blue-color;
				}
			}

			.incident-card-icon-active {
				display: none;
			}
		}

		&:hover,
		&.active {
			border: 1px solid $border-light-blue;
			background: $background-color-2;
		}

		&.active {
			.incident-card-icon-active {
				display: initial;
			}

			.incident-card-icon-inactive {
				display: none;
			}
		}

		.incident-card-icon-ticket {
			background: $dropdown-option-selected;
			border: 1px solid $border-light-blue;
			margin: 0 12px 0 8px;

			svg {
				fill: $svg-icon-blue-color;
				transform: rotate(90deg);
			}
		}

		.incident-card-content {
			display: flex;
			flex-direction: column;
			width: calc(100% - 308px);
			align-items: flex-start;

			.incident-subject {
				width: calc(100% - 56px);
				display: flex;
				font-weight: $text-weight-medium;
				margin-bottom: 4px;

				.display-id {
					margin-right: 4px;
					color: $primary-text-color-1;
				}

				.subject {
					text-transform: capitalize;
				}
			}

			.incident-card-properties {
				color: $secondary-text-color-2;
				width: 100%;
				display: flex;

				span:first-child {
					max-width: 200px;
					display: inline-block;
				}

				span:nth-child(2) {
					display: flex;
					align-items: center;
					padding-right: 4px;

					&:before {
						/* list style dot */
						content: '\25CF';
						font-size: 4px;
						color: $border-dark-color;
						padding: 0 4px;
						margin-top: 1px;
					}
				}
			}
		}
	}
}