@mixin font-size($size) {
	font-size: $size;
	line-height: $size * 1.5;
}

@mixin flexbox {
	display: -webkit-box;  /* OLD - iOS 6-, Safari 3.1-6 */
	display: -moz-box;     /* OLD - Firefox 19- (buggy but mostly works) */
	display: -ms-flexbox;  /* TWEENER - IE 10 */
	display: -webkit-flex; /* NEW - Chrome */
	display: flex;         /* NEW, Spec - Opera 12.1, Firefox 20+ */
}

// Inline flex
@mixin inline-flexbox {
	display: -webkit-inline-box;
	display: -moz-inline-box;
	display: -webkit-inline-flex;
	display: -ms-inline-flexbox;
	display: inline-flex;
}

@mixin align-items($value: stretch) {
	@if $value == flex-start {
		-webkit-box-align: start;
		-moz-box-align: start;
		-ms-flex-align: start;
	} @else if $value == flex-end {
		-webkit-box-align: end;
		-moz-box-align: end;
		-ms-flex-align: end;
	} @else {
		-webkit-box-align: $value;
		-moz-box-align: $value;
		-ms-flex-align: $value;
	}
	-webkit-align-items: $value;
	align-items: $value;
}

@mixin justify-content($value: flex-start) {
	@if $value == flex-start {
		-webkit-box-pack: start;
		-moz-box-pack: start;
		-ms-flex-pack: start;
	} @else if $value == flex-end {
		-webkit-box-pack: end;
		-moz-box-pack: end;
		-ms-flex-pack: end;
	} @else if $value == space-between {
		-webkit-box-pack: justify;
		-moz-box-pack: justify;
		-ms-flex-pack: justify;
	} @else if $value == space-around {
		-ms-flex-pack: distribute;
	} @else {
		-webkit-box-pack: $value;
		-moz-box-pack: $value;
		-ms-flex-pack: $value;
	}
	-webkit-justify-content: $value;
	justify-content: $value;
}

@mixin flex-direction($value: row) {
	@if $value == row-reverse {
		-webkit-box-direction: reverse;
		-webkit-box-orient: horizontal;
		-moz-box-direction: reverse;
		-moz-box-orient: horizontal;
	} @else if $value == column {
		-webkit-box-direction: normal;
		-webkit-box-orient: vertical;
		-moz-box-direction: normal;
		-moz-box-orient: vertical;
	} @else if $value == column-reverse {
		-webkit-box-direction: reverse;
		-webkit-box-orient: vertical;
		-moz-box-direction: reverse;
		-moz-box-orient: vertical;
	} @else {
		-webkit-box-direction: normal;
		-webkit-box-orient: horizontal;
		-moz-box-direction: normal;
		-moz-box-orient: horizontal;
	}
	-webkit-flex-direction: $value;
	-ms-flex-direction: $value;
	flex-direction: $value;
}

// since mostly common usage is center
@mixin flex-center {
	@include flexbox;
	@include justify-content(center);
	@include align-items(center);
}

@mixin inline-flex-center {
	@include inline-flexbox;
	@include justify-content(center);
	@include align-items(center);
}

// only for the most commonly used style attributes
@mixin flexbox-simple($justifyContent: flex-start, $alignItems: stretch) {
	@include flexbox;
	@include justify-content($justifyContent);
	@include align-items($alignItems);
}

@mixin wrap(){
	white-space: pre-wrap; /* css-3 */
	white-space: -moz-pre-wrap; /* Mozilla, since 1999 */
	white-space: -pre-wrap; /* Opera 4-6 */
	white-space: -o-pre-wrap; /* Opera 7 */
	word-wrap: break-word; /* Internet Explorer 5.5+ */
}

@mixin border-style($color: $border-light-color, $radius: 4px, $width: 1px) {
	border: $width solid $color;
	border-radius: $radius;
}

@mixin line-clamp($lines: 1, $line-height: null) {
	overflow: hidden;

	@if ($lines == 1) {
		// Limited to one line
		// Ellipsis will be displayed
		// Excellent browser support
		text-overflow: ellipsis;
		white-space: nowrap;
	}

	@if ($lines > 1) {
		@if ($line-height) {
			// Set max-height fallback, for browsers not supporting -webkit-line-clamp
			$max-height: #{$lines + ' * (' + $line-height + ')'};
			max-height: calc(#{$max-height});
		}

		@supports (-webkit-line-clamp: 1) {
			// Not limited to one line
			// Ellipsis will be displayed
			// Not so good browser support
			display: -webkit-box;
			-webkit-line-clamp: $lines;
			-webkit-box-orient: vertical;
			max-height: 100%; // Unset max-height fallback
		}
	}
}

@mixin fade-loader() {
	opacity: 0.6;
	cursor: not-allowed;
	pointer-events: none;
}

@mixin blue-spinner() {
	width: 16px;
	height: 16px;
	background: url(/a/assets/images/blue-spinner.svg) no-repeat center;
	background-position: center;
	background-size: 16px;
	animation: 0.9s linear spin infinite;
}

@mixin filter-active(){
	&::after {
		content: " ";
		position: absolute;
		top: -4px;
		background: $filter-active-indicator;
		width: 4px;
		left: 24px;
		height: 4px;
		padding: 3px;
		border-radius: 5px;
	}
}

@mixin highlight-card($color) {
	&::before {
		content   : " ";
		position  : absolute;
		left      : 0;
		height    : 100%;
		width     : 3px;
		background: $color;
	}
}

@mixin priority-colors($low, $medium, $high, $urgent) {
	height: 10px;
	width: 10px;
	border-radius: 2px;
	&.options{
		margin-right: 8px;
	}
	&#{$low} {
		background: $priorityLowColor;
	}
	&#{$medium} {
		background: $priorityMediumColor;
	}
	&#{$high} {
		background: $priorityHighColor;
	}
	&#{$urgent} {
		background: $priorityUrgentColor;
	}
}

@mixin subject-style() {
	border-radius: 4px;
	& .customer_responded, & .overdue, & .customer_responded_overdue  {
		background: $customerRespondedBg;
		color: $customerRespondedBorder;
		border: solid 1px $customerRespondedBorder;
		text-shadow: -1px -1px 0 rgba(113,113,113,0.53);
		box-sizing: border-box;
	}
	& .overdue{
		background: $overdueBg;
		color: $overdueColor;
		border: solid 1px $overdueBorder;
		text-shadow: none;
		box-sizing: border-box;
	}
	.customer_responded_overdue_hover{
		background: $customerRespondedOverdueHoverBg;
		height: 10px;
		width: 10px;
		border-radius: 100%;
		position: absolute;
		margin-left: 17px;
		margin-top: 0;
		box-shadow: inset 0px 0px 0px 1px $white;
	}
	& .customer_responded_overdue{
		padding-left: 20px !important;
		position:relative;
		&:before{
			content: "";
			background: $priorityUrgentColor;
			height: 10px;
			width: 10px;
			border-radius: 100%;
			position: absolute;
			margin-left: -17px;
			margin-top: 0;
			box-shadow: inset 0px 0px 0px 1px $white;
		}
		&:hover{
			.customer_responded_overdue_hover{ box-shadow: inset 0px 0px 0px 1px $app-black;}
		}
	}
	& .new {
		background: $statusNewBg;
		border: 1px solid $statusNewBorder;
		color: $statusNewColor;
		box-sizing: border-box;
	}
	& .elapsed {
		background: $overdueBg;
		color: $overdueColor;
		border: solid 1px $overdueBorder;
		box-sizing: border-box;
	}
}

@mixin transition($value: all 0.5s ease-in-out) {
	-webkit-transition: $value;
	-moz-transition: $value;
	-ms-transition: $value;
	transition: $value;
}

@mixin svg-fill($color) {
	svg {
		fill: $color;
		path { fill: $color; }
	}
}
